$stat-border-radius: 40px;

.stat {
    box-shadow: 8px 8px 16px 0 rgba(5, 70, 75, 0.08);
    width: 280px;
    padding: 16px;
    border-top-right-radius: $stat-border-radius;
    border-bottom-left-radius: $stat-border-radius;
    overflow: hidden;

    @include mobile-s {
        width: 380px;
    }

    &--realty-overview {
        width: 100%;
        padding: 24px;
    }

    &__text {
        p {
            color: $cgt-color-ui-800;
            font-size: 14px;
            line-height: 1.57;
            font-weight: normal;
    
            @include mobile-s {
                font-size: 16px;
                line-height: 1.63;
            }
        }
    }

    &__author {
        max-width: 192px;
        margin-left: auto;

        @include mobile-s {
            max-width: none;
        }

        p:last-child {
            margin-bottom: 0;
        }
    }

    &--realty-overview & {

        &__author {
            margin-left: 0;
            max-width: none;
        }
    }
}