// Name:            Avatar
// Description:     Styles for avatar
//
// Component:       `cgt-avatar`
//
// Sub-objects:     `cgt-avatar__image`
//
// Modifiers:
// `cgt-avatar--l`
// `cgt-avatar--m`
// `cgt-avatar--s`
// `cgt-avatar--xs`
//
// States:          ``

// Variables
// ====================================================
$cgt-avatar-back-color:                               $cgt-color-brand-primary-200;
$cgt-avatar-color:                                    $cgt-color-brand-primary-600;
$cgt-avatar-size-xl:                                  100px;
$cgt-avatar-size-l:                                   56px;
$cgt-avatar-size-m:                                   44px;
$cgt-avatar-size-s:                                   32px;
$cgt-avatar-size-xs:                                  24px;

.cgt-avatar {
  display: flex;
  position: relative;
  width: $cgt-avatar-size-xl;
  height: $cgt-avatar-size-xl;
  overflow: hidden;
  border-radius: 50%;
  background-color: $cgt-avatar-back-color;
  background-repeat: no-repeat;
  background-size: cover;
  color: $cgt-avatar-color;
  font-weight: 600;
  font-size: 24px;
  text-transform: uppercase;
  align-items: center;
  justify-content: center;
  line-height: $cgt-avatar-size-xl;
  vertical-align: middle;

  &--l {
    width: $cgt-avatar-size-l;
    height: $cgt-avatar-size-l;
    line-height: $cgt-avatar-size-l;
  }

  &--m {
    width: $cgt-avatar-size-m;
    height: $cgt-avatar-size-m;
    line-height: $cgt-avatar-size-m;
    font-size: 18px;
  }

  &--s {
    width: $cgt-avatar-size-s;
    height: $cgt-avatar-size-s;
    line-height: $cgt-avatar-size-s;
    font-size: 14px;
  }

  &--xs {
    width: $cgt-avatar-size-xs;
    height: $cgt-avatar-size-xs;
    line-height: $cgt-avatar-size-xs;
    font-size: 12px;
  }

  &__image {
    position: absolute;
    top: 50%;
    left: 0;
    width: 100%;
    height: 100%;
    object-fit: cover;
    transform: translateY(-50%);
  }
}
