.get-started {
    padding-top: 70px;
    padding-bottom: 50px;
    overflow: hidden;

    @include mobile-s {
        padding-top: 112px;
        padding-bottom: 112px;
    }

    &__text {
        margin-bottom: 32px;

        @include mobile-s {
            margin-bottom: 56px;
        }

        @include tablet-m {
            order: 1;
            margin-bottom: 0;
        }

        p:last-child {
            margin-bottom: 0;
        }
    }

    &__steps {
        position: relative;
    }

    &__wrap {
        align-items: center;
    }

    &__decoration-dots {
        position: absolute;
        z-index: -1;

        &:nth-child(1) {
            right: 0;
            top: -50px;

            @include mobile-s {
                top: -10px;
                right: auto;
                left: -40px;
            }
        }

        &:nth-child(2) {
            top: 47%;
            transform: translateY(-50%);
            left: -50px;

            @include mobile-s {
                display: none;
            }
        }

        &:nth-child(3) {
            bottom: 25px;
            right: -20px;

            @include mobile-s {
                right: 0px;
                bottom: -70px;
            }

            @include tablet-m {
                display: none;
            }
        }
    }
}