$header-shadow-color: rgba(179, 181, 183, 0.16);

.header {
    width: 100%;
    position: fixed;
    top: 0;
    left: 0;
    z-index: 20;
    background-color: $inner-color-white;

    @include tablet-m {
        box-shadow: 0 4px 12px 0 $header-shadow-color;
    }

    &__mobile-menu {
        position: fixed;
        width: 100%;
        height: 100%;
        left: 0;
        top: 0;
        z-index: 1;
        padding-top: $inner-global-height-header-mobile;
        background-color: $inner-color-white;
        overflow-y: auto;
        transform: translateX(-100%);
        transition: transform $global-animation-time;

        .menu-open & {
            transform: translateX(0);
        }
    }

    &__wrap {
        align-items: center;
        height: $inner-global-height-header-mobile;
        position: relative;
        z-index: 2;
        background-color: $inner-color-white;
    }

    &__logo {
        text-align: center;
        line-height: 0;

        @include tablet-m {
            text-align: left;
        }
    }

    &__contact-link {
        text-align: right;
        margin-right: -12px;

        .cgt-button {

            @include tablet-m {
                padding-left: 43px;
                padding-right: 43px;
            }
        }
    }

    &__hamburger {
        display: flex;
        flex-direction: column;
        justify-content: center;
        align-items: flex-start;
        margin-left: -10px;

        @include mobile-s {
            display: none;
        }
    }

    &__nav {
        display: none;

        @include mobile-s {
            display: block;
        }
    }
}