.converting-products {
    padding-bottom: 70px;
    overflow: hidden;

    @include mobile-s {
        padding-bottom: 112px;
    }

    &:first-child {
        padding-top: 40px;

        @include mobile-s {
            padding-top: 64px;
        }

        @include tablet-m {
            padding-top: 56px;
        }
    }

    &:not(:first-child) {
        padding-top: 70px;

        @include mobile-s {
            padding-top: 112px;
        }
    }

    &__text {
        margin-bottom: 32px;

        @include mobile-s {
            margin-bottom: 40px;
        }

        @include tablet-m {
            padding-top: 40px;
            margin-bottom: 0;
        }

        .cgt-heading-h3 {
            line-height: 1.38;

            @include mobile-s {
                line-height: 1.45;
            }

            @include tablet-m {
                line-height: 1.42;
            }
        }

        & > *:not(.converting-products__decoration-dots) {
            position: relative;
            z-index: 2;
        }
    }

    &__steps {
        position: relative;

        & > *:not(.converting-products__decoration-dots) {
            position: relative;
            z-index: 2;
        }
    }

    &__decoration-dots {
        position: absolute;
        z-index: 1;

        &:nth-child(1) {
            top: 480px;
            left: -67px;

            @include mobile-s {
                top: 25%;
                transform: translateY(-50%);
                left: 60%;
            }

            @include tablet-m {
                left: 44%;
                top: 22%;
            }
        }

        &:nth-child(2) {
            bottom: 0;
            left: -15px;

            @include mobile-s {
                left: 29%;
                bottom: -55px;
            }

            @include tablet-m {
                left: -77px;
                bottom: 216px;
            }
        }
    }

    &__text & {

        &__decoration-dots {
            &:nth-child(1) {
                top: 50%;
                left: auto;
                transform: translateY(-50%);
                right: -67px;

                @include mobile-s {
                    display: none;
                }
            }
        }
    }
}