// Name:            Rating
// Description:     Styles for rating
//
// Component:       `cgt-rating`
//
// Sub-objects:     ``
//
// Modifiers:
// `cgt-input--1`
// `cgt-input--1-2`
// `cgt-input--2`
// `cgt-input--2-2`
// `cgt-input--3`
// `cgt-input--3-2`
// `cgt-input--4`
// `cgt-input--4-2`
// `cgt-input--5`
// `cgt-input--read-only`
//
// Variables
// =====================================
$cgt-rating-color-default:            $cgt-color-ui-500;
$cgt-rating-color-rating:             $cgt-color-brand-primary-600;

@mixin cgt-rating-selected($number, $half: false) {
  > div:nth-child(-n+#{$number}) {
    .fal {
      color: $cgt-rating-color-rating;
      font-weight: 900;
    }
  }

  @if($half) {
    > div:nth-child(#{$number}) {
      .fal {
        display: none;
      }

      .fas {
        display: inline-block;
      }
    }
  }
}

.cgt-rating {
  display: inline-flex;
  line-height: 0;
  font-size: 0;
  align-items: flex-start;

  > * {
    width: 26px;
    text-align: center;

    &:last-child {
      width: 18px;
    }

    &:not(:last-child) {
      padding-right: 8px;
    }
  }

  input {
    position: absolute;
    visibility: hidden;
    z-index: -1;
    width: 0;
    height: 0;
    margin: 0;
    padding: 0;
    pointer-events: none;
    touch-action: none;
  }

  i {
    color: $cgt-rating-color-default;
    font-size: $global-text-size-medium;
    cursor: pointer;

    &.fas {
      display: none;
      color: $cgt-rating-color-rating;
    }
  }

  &--1 {
    @include cgt-rating-selected(1);
  }

  &--1-2 {
    @include cgt-rating-selected(2, true);
  }

  &--2 {
    @include cgt-rating-selected(2);
  }

  &--2-2 {
    @include cgt-rating-selected(3, true);
  }

  &--3 {
    @include cgt-rating-selected(3);
  }

  &--3-2 {
    @include cgt-rating-selected(4, true);
  }

  &--4 {
    @include cgt-rating-selected(4);
  }

  &--4-2 {
    @include cgt-rating-selected(5, true);
  }

  &--5 {
    @include cgt-rating-selected(5);
  }

  &--read-only {

    i {
      cursor: default;
    }
  }

  &:not(.cgt-rating--read-only):hover {

    > * i {
      color: $cgt-rating-color-rating;
      font-weight: 900;
    }

    > *:hover ~ div i {
      color: $cgt-rating-color-default;
      font-weight: 300;
    }

    .fas {
      display: none !important;
    }

    .fal {
      display: inline-block !important;
    }
  }
}
