.reviews {
    background-color: $inner-global-background-section-ui;
    padding: 70px 0;
    position: relative;
    overflow: hidden;

    @include mobile-s {
        padding: 112px 0;
    }

    &__placeholder-wrap {
        display: block;
        line-height: 0;

        img {
            width: 100%;
            border-radius: 4px;
            overflow: hidden;
            box-shadow: 8px 8px 16px 0 rgba(5, 70, 75, 0.08);

            @include mobile-s {
                border-top-right-radius: 40px;
                border-bottom-left-radius: 40px;
            }
        }
    }

    &__pagination {
        margin-top: 20px;

        @include mobile-s {
            margin-top: 65px;
        }
    }

    &__button {
        display: none;

        @include mobile-s {
            display: flex;
            justify-content: space-between;
            transform: translateY(-65%);
            touch-action: none;
            pointer-events: none;
            max-width: 368px;
            margin: 0 auto;
        }

        @include tablet-m {
            position: absolute;
            left: 0;
            top: 50%;
            width: 100%;
            transform: translateY(-50%);
            margin-top: -60px;
            max-width: none;
            z-index: 10;
        }
    }

    &__logo {
        line-height: 0;
        margin-bottom: 32px;

        @include mobile-s {
            margin-bottom: 24px;
        }
    }

    &__text {
        margin-bottom: 20px;

        @include mobile-s {
            margin-bottom: 56px;
        }

        @include tablet-m {
            margin-bottom: 0;
        }
    }

    &__quote {
        @include mobile-s {
            margin-bottom: 16px;
        }
    }

    &__slide-wrap {

        @include tablet-m {
            align-items: center;
        }
    }

    &__decoration-dots {
        position: absolute;
        z-index: 0;
        pointer-events: none;
        touch-action: none;

        &:nth-child(1) {
            bottom: 250px;
            left: 0;

            @include mobile-s {
                display: none;
            }

            @include tablet-m {
                display: block;
                left: 4%;
                top: 112px;
            }
        }

        &:nth-child(2) {
            bottom: 70px;
            right: 5px;

            @include mobile-s {
                bottom: 150px;
                right: -110px;
            }

            @include tablet-m {
                right: 4%;
                bottom: 100px;
            }
        }
    }
}