// Name:            Card
// Description:     Component to create boxed content containers
//
// Component:       `cgt-card`
//
// Modifiers:
// `cgt-card--shadow-100`
// `cgt-card--border`

// Variables
// ============================================
$cgt-card-background-color-default:           $cgt-color-ui-100;
$cgt-card-shadow-color:                       $cgt-color-brand-secondary-600;

.cgt-card {
  position: relative;
  margin: 0;
  background-color: $cgt-card-background-color-default;
  padding: 0;
  transition-duration: $global-animation-time;
  transition-property: box-shadow, border-color;


  &--shadow-100 {
    box-shadow: 0 2px 6px 0 rgba($cgt-card-shadow-color, 0.12);
  }

  &--border {
    border: 1px solid $border-color-primary;

    &.is-focus {
      border-color: $border-color-focus;
    }

    &.cgt-card--focus {
      &:focus {
        border-color: $border-color-focus;
      }
    }
  }

  &--hover {
    &:hover {
      box-shadow: 0 8px 12px 0 rgba($cgt-card-shadow-color, 0.12);
    }
  }

  &--focus {
    &:focus {
      box-shadow: 0 0 0 4px $shadow-focus;
    }
  }

  &.is-focus {
    box-shadow: 0 0 0 4px $shadow-focus;
  }

  &__badge {
    display: flex;
    position: absolute;
    top: 0;
    right: 0;
    z-index: 10;
    max-width: 100%;
    flex-wrap: wrap;
  }

  &__header {
    border-bottom: 1px solid $cgt-color-ui-400;
    padding: 16px;

    .cgt-heading-h6 {
      color: $cgt-color-ui-700;
    }

    @include media-breakpoint-up(md) {
      padding: 24px;
    }
  }

  &__content {
    padding: 16px;

    @include media-breakpoint-up(md) {
      padding: 16px 24px 24px;
    }
  }
}

.cgt-collapsible-card {
  &__trigger {
    padding: 20px 16px;

    @include media-breakpoint-up(md) {
      padding: 20px 24px;
    }
  }

  &__content {
    padding: 0 16px 16px;

    @include media-breakpoint-up(md) {
      padding: 0 24px 16px 24px;
    }
  }

  &.is-collapsed {
    .cgt-collapsible-card__content {
      display: none;
    }
  }
}

// Name:            Card-container
// Description:     component that provides specific spacings for card
//
// Component:       `cgt-cards-list-container`

.cgt-cards-list-container {
  margin-bottom: 36px;

  .cgt-card + .cgt-card {
    margin-top: 8px;
  }
}

// Name:            Item card
// Description:     Component to be used inside of cgt-card, brings content fields like preview, actions, info...
//
// Component:       `cgt-item-card`
//
// Elements:
// `cgt-item-card__info`
// `cgt-item-card__preview`
// `cgt-item-card__actions`
// `cgt-item-card__price`
// `cgt-item-card__title`

// States:
// `is-updating`

// Variables
// ============================================
$cgt-card-preview-size: 74px;
$cgt-card-preview-size-m: 92px;

.cgt-item-card {
  display: flex;
  padding: 8px;

  &__info,
  &__preview,
  &__actions {
    width: 100%;
  }

  &__preview {
    display: inline-block;
    width: 100%;
    max-width: $cgt-card-preview-size;
    line-height: 0;

    img {
      max-width: 100%;
      height: auto;
    }
  }

  &__info {
    padding-left: 12px;
    line-height: 1.5;
  }

  &__price {
    display: inline-block;
    margin-right: 8px;

    &--primary {
      color: $cgt-color-brand-primary-600;
    }

    &--crossed {
      text-decoration: line-through;
    }
  }

  &__actions {
    display: flex;
    max-width: 32px;
    flex-flow: column;
    align-items: center;
    justify-content: space-between;

    i {
      display: inline-block;
      color: $cgt-color-ui-400;
    }
  }

  &__title {
    display: inline-block;
    margin: 0;
  }

  &__title-badges {
    display: inline-block;
    margin-left: 8px;
  }

  @include media-breakpoint-up(md) {
    &__preview {
      max-width: $cgt-card-preview-size-m;
    }

    &__info {
      display: flex;
      padding-left: 16px;
      justify-content: space-between;
    }

    &__info-block {
      &:first-of-type {
        padding-right: 20px;
      }
    }
  }

  &.is-updating {
    opacity: 0.5;
    pointer-events: none;
  }
}

.cgt-toggle-icon {
  i {
    &:last-of-type {
      display: none;
    }
  }

  &:hover {
    cursor: pointer;

    i {
      &:first-of-type {
        display: none;
      }

      &:last-of-type {
        display: inline-block;
        color: $cgt-color-utility-danger-600;
      }
    }
  }
}
