.cgt-loader {
  position: relative;
  height: 4px;
  border-radius: 1px;
  background-color: $global-loader-rail-color;

  &::before {
    display: inline-block;
    position: absolute;
    z-index: 2;
    width: 0;
    height: 100%;
    border-radius: 1px;
    background-color: $global-loader-track-color;
    transition-property: width;
    transition-duration: $global-animation-time;
    content: '';
  }

  @for $i from 0 through 100 {
    &[data-percent='#{$i}']::before {
      width: percentage($i) / 100;
    }
  }
}
