// Name:            Search
// Description:     Styles for input search
//
// Component:       `cgt-search`
//
// Sub-objects:     `cgt-search__dropdown`
//
// Modifiers:       `cgt-search--clear`
//
// States:
// `is-autocomplete`
// `is-focus` for 'li' tag
//
// Variables
// ===================================
$cgt-search-color-dropdown:                   $cgt-color-ui-800;
$cgt-search-shadow-dropdown:                  $cgt-color-ui-400;
$cgt-search-border-color-dropdown:            $cgt-color-ui-400;
$cgt-search-back-color-dropdown:              $cgt-color-ui-100;
$cgt-search-back-color-dropdown-focus-item:   $cgt-color-ui-300;
$cgt-search-icon-color-search:                $cgt-color-ui-700;
$cgt-search-icon-color-clear:                 $cgt-color-ui-500;

.cgt-search {
  position: relative;

  &__dropdown {
    display: none;
    position: absolute;
    top: calc(100% + 4px);
    z-index: 5;
    width: 100%;
    margin: 0;
    border: solid 1px $cgt-search-border-color-dropdown;
    background-color: $cgt-search-back-color-dropdown;
    padding: 0;
    box-shadow: 0 2px 6px 0 $cgt-search-shadow-dropdown;

    li {
      padding: 12px 16px;
      color: $cgt-search-color-dropdown;
      font-weight: normal;
      font-size: $global-text-size-medium;
      line-height: normal;
      list-style-type: none;
      transition-property: background-color;
      transition-duration: $global-animation-time;
      cursor: pointer;

      &:focus,
      &.is-focus,
      &:hover {
        outline: none;
        background-color: $cgt-search-back-color-dropdown-focus-item;
      }

      span {
        font-weight: 600;
      }
    }
  }

  i {
    position: absolute;
    top: 50%;
    z-index: 1;
    transform: translateY(-50%);
  }

  .fa-search {
    left: 16px;
    color: $cgt-search-icon-color-search;
  }

  .fa-times-circle {
    display: none;
    right: 16px;
    color: $cgt-search-icon-color-clear;
  }

  input {
    padding-right: 44px;
    padding-left: 44px;
  }

  &--clear {
    .fa-times-circle {
      display: inline-block;
    }
  }

  &.is-autocomplete & {

    &__dropdown {
      display: block;
    }
  }
}
