.form-wrapper {
    background-color: $cgt-color-ui-100;
    box-shadow: 8px 8px 16px 0 rgba(5, 70, 75, 0.08);
    border-top-right-radius: 40px;
    border-bottom-left-radius: 40px;
    padding: 40px 16px;

    form {
        width: 100%;
        max-width: 498px;
        margin: 0 auto;

        @include mobile-s {
            max-width: none;
            padding-left: 80px;
            padding-right: 80px;
        }
    }

    label {
        font-size: 12px;
        font-weight: 600;
        line-height: normal;
        color: $cgt-color-ui-600;
    }

    input:not([type="submit"]) {
        width: 100%;
        height: 44px;
        margin: 0;
        outline: none;
        border: 1px solid $cgt-input-border-color;
        border-radius: 1px;
        background-color: $cgt-input-back-color;
        padding-right: 16px;
        padding-left: 16px;
        color: $cgt-input-color;
        font-family: $global-font-family-primary;
        font-size: $global-text-size-medium;
        font-weight: normal;
        transition-duration: $global-animation-time;
        transition-property: border-color, box-shadow;
        appearance: none;

        &::placeholder {
          color: $cgt-input-placeholder-color;
        }

        &:focus {
          @include global-focus();
        }
    }

    input.is-invalid {
        border-color: $cgt-input-theme-error;
    }

    .error-message {
        padding: 0;
        margin: 0;

        li {
            list-style: none;
        }

        .hs-error-msg {
            width: 100%;
            padding-top: 8px;
            font-size: $global-text-size-small;
            line-height: normal;
            font-weight: 600;
            color: $cgt-input-theme-error;

            &::first-letter {
              text-transform: uppercase;
            }
        }
    }

    select {
        // @include cgt-select-style-value();
        width: 100% !important;
        height: 44px !important;
        border-width: 1px;
        border-style: solid;
        border-radius: 1px !important;
        border-color: $cgt-select-border-color !important;
        background-color: $cgt-select-back-color !important;
        padding-right: 16px !important;
        padding-left: 16px !important;
        transition-property: background-color;
        transition-duration: $global-animation-time;
        cursor: pointer;
    
        &:focus {
          @include global-focus();
          outline: none;
        }
    
        &:not(.cgt-select__control--is-disabled):hover {
          background-color: $cgt-select-back-color-hover !important;
        }
    
        &:disabled {
          color: $cgt-select-disabled-theme !important;
          cursor: not-allowed !important;
    
          &:hover {
            background-color: $cgt-select-back-color !important;
          }
        }
    }

    input[type="submit"] {
        @extend .cgt-button;
        @extend .cgt-button--primary;
        width: 100%;
    }

    .hs-form-field {
        margin-bottom: 20px;
    }

    .hs-submit {
        margin-top: 32px;
    }
}