.steps {
    padding: 0;
    margin: 0;

    @include mobile-s {
        display: flex;
        flex-wrap: wrap;
        justify-content: center;
        margin: 0 -15px;
    }

    &__item {
        list-style-type: none;
        margin: 0 auto 20px;

        @include mobile-s {
            width: calc(50% - 30px);
            margin: 0 15px 0;
            max-width: 284px;
        }

        &:nth-child(1) {
            @include mobile-s {
                margin-top: 0;
            }
        }

        &:nth-child(2) {
            @include mobile-s {
                margin-top: 40px;
            }
        }

        &:nth-child(even) {
            @include mobile-s {
                margin-top: 30px;
            }
        }

        &:nth-child(odd) {
            @include mobile-s {
                margin-top: -10px;
            }
        }

        &:last-child:not(:nth-child(even)) {
            @include mobile-s {
                margin-top: 30px;
            }
        }

        &:nth-child(2n) {

            .step {
                @include mobile-s {
                    border-radius: 0;
                    border-top-right-radius: 40px;
                }
            }
        }

        &:nth-child(3n) {
            .step {
                @include mobile-s {
                    border-radius: 0;
                    border-bottom-left-radius: 40px;
                }
            }
        }

        &:nth-child(4n) {
            .step {
                @include mobile-s {
                    border-radius: 0;
                    border-bottom-right-radius: 40px;
                }
            }
        }

        &:nth-child(5n),
        &:first-child {
            .step {
                @include mobile-s {
                    border-radius: 0;
                    border-top-left-radius: 40px;
                }
            }
        }
    }
}