.description {

    .cgt-heading-h1 {
        line-height: 1.38;

        @include mobile-s {
            line-height: 1.45;
        }

        @include tablet-m {
            line-height: 1.42;
        }
    }

    .cgt-heading-h3 {
        line-height: 1.33;

        @include mobile-s {
            line-height: 1.31;
        }
    }

    &__link {
        margin-top: 20px;
    }

    ul:not([class]) {
        margin: 12px 0;
        padding-left: 0;
        width: 80%;
        display: flex;
        flex-wrap: wrap;

        @include tablet-m {
            width: 60%;
        }
    }

    li:not([class]) {
        list-style-type: none;
        position: relative;
        padding-left: 20px;
        width: 50%;

        @include mobile-s {
            width: auto;
            margin-right: 64px;
        }

        @include tablet-m {
            margin-right: 0;
            padding-right: 24px;
            width: 50%;
        }

        &::before {
            content: '';
            display: inline-block;
            width: 6px;
            height: 6px;
            border-radius: 1px;
            background-color: $cgt-color-brand-primary-600;
            position: absolute;
            left: 0;
            top: 10px;
        }
    }
}