.cgt-tag {

    &--popular-plan {
        position: absolute;
        left: calc(-60vw + 20px);
        z-index: 3;
        top: 0;
        transform: translateY(-100%);
        width: calc(100vw - 40px);
        height: 26px;
        justify-content: center;
        text-align: center;
        opacity: 0;
        transition: opacity 0.3s;

        @include mobile-s {
            opacity: 1 !important;
            width: calc(100% + 1px);
            left: -1px;
        }
    }
}