// Name:            Navbar
// Description:     Styles for main navigation bar
//
// Component:       `cgt-navbar`
//
// Sub-objects:
//
// Modifiers:
// States:
//
// Variables
// ========================================
$cgt-navbar-background-color: $cgt-color-ui-800;

.cgt-navbar {
  position: fixed;
  top: 0;
  z-index: 452;
  width: 100%;

  &__container {
    display: flex;
    width: 100%;
    height: 48px;
    background-color: $cgt-navbar-background-color;
  }

  &__logo {
    display: block;
    width: 144px;
    height: 48px;
    padding: 12px 24px;

    img {
      max-width: 100%;
    }
  }

  @include media-breakpoint-up(md) {
    &__container {
      height: 60px;
    }

    &__logo {
      width: 200px;
      height: 60px;
      padding: 16px 44px;
    }
  }
}
