.link {
    position: relative;
    font-weight: 600;

    span,
    i {
        vertical-align: middle;
    }

    i {
        margin-left: 12px;
        transition: transform $global-animation-time;
        position: relative;
        z-index: 2;

        &::before {
            vertical-align: middle;
            position: relative;
            z-index: 1;
        }
    }

    &__decoration {
        width: 16px;
        height: 16px;
        background-color: #d1f2f4;
        display: inline-block;
        vertical-align: middle;
        position: absolute;
        right: -13px;
        top: 50%;
        transform: translateY(-50%);
        z-index: 1;
        transition: right $global-animation-time;
        margin-top: 3px;
    }

    .sprite-arrow-forward + &__decoration {
        right: -16px;
        margin-top: 2px;

        @include mobile-s {
            right: -8px;
        }
    }

    &:hover {
        i {
            transform: translateX(5px);
        }

        .link__decoration {
            right: -18px;
        }

        .sprite-arrow-forward + .link__decoration {
            right: -13px;
        }
    }
}