html {
  box-sizing: border-box;
}

*,
*::before,
*::after {
  box-sizing: inherit;
}

body {
  margin: 0;
  background-color: $global-background-color;
  color: $cgt-text-default;
  font-family: $global-font-family-primary;
  font-size: $global-text-size-medium;
  font-weight: normal;
  line-height: 1.75;
}

img {
  max-width: 100%;
}

// Reset styles for input
input {
  font-family: inherit;
}

textarea {
  resize: vertical;
}

.visuallyhidden:not(:focus):not(:active) {
  position: absolute;
  width: 1px;
  height: 1px;
  overflow: hidden;
  margin: -1px;
  border: 0;
  padding: 0;
  white-space: nowrap;
  clip-path: inset(100%);
  clip: rect(0 0 0 0);
}
