$FontPathSourceSansPro: "../fonts/SourceSansPro/" !default;
$FontPathFontAwesome: "../fonts/FontAwesome/" !default;

$fa-font-path: $FontPathFontAwesome;

@import 'fontawesome/fontawesome';
@import 'fontawesome/brands';
@import 'fontawesome/light';
@import 'fontawesome/regular';
@import 'fontawesome/solid';
@import 'fontawesome/duotone';

// source-sans-pro-300 - latin_latin-ext
@font-face {
  font-family: 'Source Sans Pro';
  font-style: normal;
  font-weight: 300;
  src: url('#{$FontPathSourceSansPro}/source-sans-pro-v11-latin_latin-ext-300.eot'); // IE9 Compat Modes
  src: local('Source Sans Pro Light'), local('SourceSansPro-Light'),
  url('#{$FontPathSourceSansPro}/source-sans-pro-v11-latin_latin-ext-300.eot?#iefix') format('embedded-opentype'), // IE6-IE8
  url('#{$FontPathSourceSansPro}/source-sans-pro-v11-latin_latin-ext-300.woff2') format('woff2'), // Super Modern Browsers
  url('#{$FontPathSourceSansPro}/source-sans-pro-v11-latin_latin-ext-300.woff') format('woff'), // Modern Browsers
  url('#{$FontPathSourceSansPro}/source-sans-pro-v11-latin_latin-ext-300.ttf') format('truetype'), // Safari, Android, iOS
  url('#{$FontPathSourceSansPro}/source-sans-pro-v11-latin_latin-ext-300.svg#SourceSansPro') format('svg'); // Legacy iOS
}
