.metrics {
    padding-top: 70px;
    padding-bottom: 50px;
    background-color: $inner-global-background-section-ui;
    position: relative;

    @include mobile-s {
        overflow: hidden;
        padding-top: 112px;
        padding-bottom: 82px;
    }

    &__list {
        padding: 0;
        margin: 0;
    }

    &__item {
        list-style-type: none;
        margin-bottom: 32px;

        @include mobile-s {
            margin-bottom: 42px;
        }

        @include tablet-m {
            margin-bottom: 46px;
        }
    }

    &__text {

        @include mobile-s {
            margin-bottom: 30px;
        }
    }

    h3 {
        line-height: 1.33;
    }

    &__decoration-dots {
        position: absolute;
        z-index: 0;
        bottom: -35px;
        right: 62px;

        @include mobile-s {
            right: -120px;
            top: 260px;
            bottom: auto;
        }

        @include tablet-m {
            right: 40px;
            top: 90px;
        }
    }
}