// Name:            Modal
// Description:     Styles for modal
//
// Component:       `cgt-modal`
//
// Variables
// ========================================
$cgt-modal-backdrop-color-default:          $cgt-color-ui-800;
$cgt-modal-background-color-default:        $cgt-color-ui-100;
$cgt-modal-z-index-default:                 $global-modal-z-index;

// React Overlay Modal
.ReactModal__Body--open {
  overflow: hidden;
}

.cgt-modal--background {
  position: fixed;
  top: 0;
  right: 0;
  bottom: 0;
  left: 0;
  z-index: $cgt-modal-z-index-default;
  overflow: auto;
  background-color: rgba($cgt-modal-backdrop-color-default, 0.8);
}

.cgt-modal--container {
  display: flex;
  max-width: 472px;
  margin: auto;
  outline: none;
  pointer-events: none;
  cursor: default;
  justify-content: center;

  @include media-breakpoint-down(sm) {
    width: 100%;
  }
}

.cgt-modal {
  position: relative;
  width: 90%;
  min-width: 0;
  margin: 72px auto;
  border-radius: 1px;
  background-color: $cgt-modal-background-color-default;
  box-shadow: 0 2px 6px rgba($cgt-color-brand-secondary-600, 0.12);
  pointer-events: auto;

  @include media-breakpoint-up(lg) {
    margin-top: 156px;
  }
}

.cgt-modal__close {
  position: absolute;
  top: -40px;
  right: -16px;
  pointer-events: auto;
}

.cgt-modal__header {
  padding: 24px 12px 0;

  @include media-breakpoint-up(sm) {
    padding: 24px 32px 0;
  }

  i {
    margin-right: 16px;
  }
}

.cgt-modal__content {
  padding: 24px 12px;

  @include media-breakpoint-up(sm) {
    padding: 24px 32px;
  }
}
