// Name:            Tab
// Description:     Styles for tabs
//
// Component:       `cgt-tab`
//
// Sub-objects:
//
// Modifiers:
//
// States:          `is-active-tab`

// Variables
// ====================================================
$cgt-tab-padding-between-elements:      32px;
$cgt-tab-color-label:                   $cgt-color-ui-600;
$cgt-tab-color-label-hover:             $cgt-color-brand-primary-600;
$cgt-tab-color-label-is-active:         $cgt-color-ui-800;
$cgt-tab-underline-color:               $cgt-color-ui-400;
$cgt-tab-underline-color-is-active:     $cgt-color-brand-primary-600;

.cgt-tabs {
  display: flex;
  margin: 0;
  padding: 0;
  overflow-x: auto;

  > * {
    position: relative;
    list-style: none;
    flex-grow: 1;
    flex-basis: auto;

    &::before {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 1px;
      background-color: $cgt-tab-underline-color;
      content: '';
    }
  }

  &__item {
    display: block;
    position: relative;
    padding: $cgt-tab-padding-between-elements / 2;
    color: $cgt-tab-color-label;
    font-size: 16px;
    line-height: 1.25;
    font-weight: normal;
    text-decoration: none;
    text-align: center;
    transition-duration: $global-animation-time;
    transition-property: color;

    &:hover {
      color: $cgt-tab-color-label-hover;
    }

    &::after {
      position: absolute;
      bottom: 0;
      left: 0;
      width: 100%;
      height: 2px;
      background-color: transparent;
      content: '';
      transition-duration: $global-animation-time;
      transition-property: background-color;
    }

    // Active tab state
    &.is-active {
      color: $cgt-tab-color-label-is-active;
      font-weight: 600;
      cursor: default;

      &::after {
        background-color: $cgt-tab-underline-color-is-active;
      }
    }
  }
}

.cgt-tab-content {
  display: none;

  &.is-active {
    display: block;
  }
}

.cgt-tabs-wrapper {
  .cgt-tab-content {
    display: block;
    padding: 24px 12px;

    @include media-breakpoint-up(sm) {
      padding: 24px 32px;
    }
  }
}
