.pricing {
    background-color: $inner-global-background-section-ui;
    padding: 70px 0;
    position: relative;
    overflow: hidden;

    @include mobile-s {
        padding: 112px 0;
    }

    &__wrap {
        position: relative;
        z-index: 3;
    }

    &__table-wrap {
        overflow: hidden;
        box-shadow: 0 10px 20px 0 rgba(5, 70, 75, 0.06);
        border-radius: 8px;
        scroll-behavior: smooth;
        position: relative;
        padding-top: 26px;
        border: 1px solid transparent;
        transition: border-color 0.3s, background-color 0.3s;
        background-color: #fff;

        @include mobile-s {
            overflow: visible;
            border-radius: 0;
            border-top-left-radius: 48px;
            border-bottom-right-radius: 48px;
            box-shadow: 0 24px 48px 0 rgba(5, 70, 75, 0.08);
            padding-top: 0;
            background-color: transparent;
            border: none;
        }

        &.is-popular-plan {
            border: 1px solid $cgt-color-brand-primary-600;
            background-color: $cgt-color-brand-primary-600;

            @include mobile-s {
                border: none;
                background-color: transparent;
            }
        }
    }

    &__tr {
        display: flex;
        border-bottom: 1px solid;
        border-color: #d1f2f4;

        &:last-child {
            border-bottom: none;
        }

        & > * {
            flex-shrink: 0;
        }

        &--blue-100 {
            .pricing__th,
            .pricing__td {
                background-color: #f6fcfd;
            }
        }

        &--blue-200 {
            .pricing__th,
            .pricing__td {
                background-color: #edfafb;
            }
        }

        &--blue-300 {
            .pricing__th,
            .pricing__td {
                background-color: #e3f7f8;
            }
        }
    }

    &__th,
    &__td {
        background-color: $inner-color-white;
        min-height: 80px;
        display: flex;
        align-items: center;
        color: $cgt-color-ui-800;
        font-weight: normal;
        width: calc(40vw - 20px);

        @include mobile-s {
            width: calc(100% / 5 - 10px);
            min-height: 98px;
        }

        &--flex-center {
            justify-content: center;

            @include mobile-s {
                justify-content: flex-start;
            }
        }


        &--popular-plan {

            @include mobile-s {
                border-left: 1px solid $cgt-color-brand-primary-600 !important;
                margin-left: -1px;

                &:not(:last-child) {
                    border-right-color: $cgt-color-brand-primary-600 !important;
                }
            }

        }

        strong,
        b {
            font-weight: 600;
        }
    }

    &__th {
        padding: 12px 20px;

        &:first-child {
            width: calc(60vw - 20px);
            position: sticky;
            left: 0;

            @include mobile-s {
                position: static;
                width: calc(100% / 5 + 40px);
            }
        }

        &--popular-plan {

            @include mobile-s {
                color: $cgt-color-brand-primary-600;
            }
        }
    }

    &__td {
        padding: 12px;
        line-height: 1.5;

        @include mobile-s {
            padding: 12px 16px;
        }

        &:not(:last-child) {

            @include mobile-s {
                border-right: 1px solid;
                border-color: #d1f2f4;
            }
        }
    }

    &__button-group {
        display: flex;
        margin-bottom: 32px;

        @include mobile-s {
            display: none;
        }

        .cgt-button {
            flex-shrink: 1;
            flex-grow: 1;
            width: 100%;
            font-size: 13px;
            padding: 0;
            height: 48px;
            display: flex;
            align-items: center;
            justify-content: center;
            border-top: none;
            border-bottom: none;
            border-color: #f8f8f8;

            &:focus {
                box-shadow: none;
            }

            &:active,
            &:hover {
                background-color: $inner-color-white;
            }

            &:first-child {
                border-top-left-radius: 8px;
                border-bottom-left-radius: 8px;
                border-left: none;
            }

            &:last-child {
                border-top-right-radius: 8px;
                border-bottom-right-radius: 8px;
                border-right: none;
            }

            &.active {
                background-color: #d1f2f4;
                color: #19bcc9;
            }
        }
    }

    &__thead {
        border-bottom: 1px solid;
        border-color: #d1f2f4;
        background-color: $inner-color-white;

        @include mobile-s {
            border-top-left-radius: 48px;
            overflow: hidden;
        }
    }

    &__thead & {

        &__tr {
            width: calc(40vw - 20px);
            overflow: hidden;
            transition: width 0.3s;
            border: none;

            @include mobile-s {
                width: 100%;
            }
        }

        &__th {
            min-height: 66px;
            font-size: 20px;
            line-height: 1.9;
            font-weight: 600;

            @include mobile-s {
                min-height: 76px;
            }

            @include tablet-m {
                font-size: 24px;
                line-height: 1.58;
            }

            &:not(:first-child) {
                position: relative;
                left: calc(-60vw + 20px);

                @include mobile-s {
                    left: 0;
                    padding: 0px 16px;
                }
            }

            &:not(:last-child) {

                @include mobile-s {
                    border-right: 1px solid #d1f2f4;
                }
            }
        }
    }

    &__tbody {

        @include mobile-s {
            border-bottom-right-radius: 48px;
            overflow: hidden;
        }
    }

    &__tbody & {

        &__th {
            border-right: 1px solid;
            border-color: #d1f2f4;

            @include mobile-s {
                line-height: 1.44;
            }

            @include tablet-m {
                line-height: 1.75;
            }
        }

        &__tr {

            &:last-child {

                .pricing__td--popular-plan {

                    @include mobile-s {
                        border-bottom: 1px solid $cgt-color-brand-primary-600;
                    }
                }
            }
        }
    }

    &__tsetting {
        height: 0;
    }

    &__tsettings & {
        &__tr {
            align-items: stretch;
        }

        &__th,
        &__td {
            min-height: 0;
            height: 0;
            padding: 0;
            position: relative;
        }
    }

    &__title {
        margin-bottom: 32px;

        @include mobile-s {
            margin-bottom: 68px;
        }

        @include tablet-m {
            margin-bottom: 40px;
        }
    }

    &__decoration-dots {
        position: absolute;
        z-index: 1;

        &:nth-child(1) {
            top: 170px;
            left: -15px;

            @include mobile-s {
                top: 171px;
                left: auto;
                right: -65px;
            }

            @include tablet-m {
                top: 290px;
                right: auto;
                left: 0;
            }

            @include desktop-xl {
                left: calc((100vw - 1140px) / 2 - 110px);
            }
        }

        &:nth-child(2) {
            bottom: 87px;
            right: -10px;

            @include mobile-s {
                right: auto;
                left: -80px;
                bottom: 62px;
            }

            @include tablet-m {
                left: auto;
                right: -50px;
                bottom: 210px;
            }

            @include desktop-xl {
                right: calc((100vw - 1140px) / 2 - 155px);
            }
        }
    }
}