.advantages {
    background-color: $inner-global-background-section-ui;
    padding: 70px 0;
    position: relative;
    overflow: hidden;

    @include mobile-s {
        padding-top: 112px;
        padding-bottom: 112px;
    }

    &__item {
        background-color: $inner-color-white;
        padding: 32px 20px 20px;
        border-top-left-radius: 40px;
        border-bottom-right-radius: 40px;
        overflow: hidden;

        @include mobile-s {
            padding-left: 32px;
            padding-right: 32px;
        }

        &:not(:last-child) {
            margin-bottom: 20px;

            @include mobile-s {
                margin-bottom: 30px;
            }

            @include tablet-m {
                margin-bottom: 0;
            }
        }

        &:nth-child(even) {

            @include mobile-s {
                border-top-left-radius: 0;
                border-bottom-right-radius: 0;
                border-top-right-radius: 40px;
                border-bottom-left-radius: 40px;
            }

            @include tablet-m {
                grid-area: left-advantage;
            }
        }

        &:nth-child(odd) {
            @include tablet-m {
                grid-column: 2 / 2;
            }
        }
    }

    &__wrap {
        position: relative;
        z-index: 3;

        @include tablet-m {
            display: grid;
            align-items: center;
            grid-template-columns: 40% 60%;
            grid-template-rows: auto;
            grid-column-gap: 30px;
            grid-row-gap: 30px;
            grid-template-areas: 
                "left-advantage none"
                "left-advantage none";
        }
    }

    &__decoration-dots {
        position: absolute;
        z-index: 1;

        &:nth-child(1) {
            top: 30%;
            left: -10px;

            @include mobile-s {
                top: 85px;
                left: -80px;
            }

            @include tablet-m {
                top: 23%;
                left: 23%;
            }
        }

        &:nth-child(2) {
            bottom: 30%;
            right: -10px;

            @include mobile-s {
                right: -30px;
            }

            @include tablet-m {
                display: none;
            }
        }
    }
}