.contact-us {
    padding: 70px 0;
    overflow: hidden;

    @include mobile-s {
        padding: 112px 0;
    }

    &__text {
        margin-bottom: 32px;

        @include mobile-s {
            margin-bottom: 40px;
        }

        @include tablet-m {
            margin-bottom: 0;
        }

        h3 {
            line-height: 1.33;
        }
    }

    &:not(.contact-us--solutions-lets-talk) {
        .cgt-heading-h3 {
            font-size: 32px;
            font-weight: 600;
            line-height: 1.38;
    
            @include mobile-s {
                font-size: 44px;
                line-height: 1.45;
            }
    
            @include tablet-m {
                font-size: 48px;
                line-height: 1.42;
            }
        }
    }

    &__decoration-dots {
        position: absolute;
        z-index: 0;
        pointer-events: none;
        touch-action: none;

        &:nth-child(1) {
            right: -10px;
            top: -34px;

            @include mobile-s {
                right: -20px;
                top: -90px;
            }

            @include tablet-m {
                right: auto;
                left: -135px;
                top: 50%;
                margin-top: -98px;
            }
        }

        &:nth-child(2) {
            bottom: -11px;
            left: -1px;

            @include mobile-s {
                bottom: -47px;
                left: -18px;
            }

            @include tablet-m {
                display: none;
            }
        }
    }

    &__form {
        position: relative;

        .form-wrapper {
            position: relative;
            z-index: 10;
        }
    }

    &--solutions-lets-talk {
        background-color: #fff;
    }

    &--solutions-lets-talk & {

        &__text {
            @include mobile-s {
                flex: 0 0 90%;
                max-width: 90%;
            }

            @include tablet-m {
                flex: 0 0 41.66667%;
                max-width: 41.66667%;
            }

            h3 {

                @include mobile-s {
                    font-size: 32px;
                }
            }
        }
    }
}