// Variables
// ================================
$cgt-breadcrumb-font-size:                    14px;
$cgt-breadcrumb-text-color:                   $cgt-color-ui-500;
$cgt-breadcrumb-text-color-hover:             $cgt-color-brand-primary-600;
$cgt-breadcrumb-padding-between-item:         12px;

@mixin typography-style() {
  color: $cgt-breadcrumb-text-color;
  font-size: $cgt-breadcrumb-font-size;
  font-weight: normal;
  line-height: normal;
}

.cgt-breadcrumb {
  display: flex;
  margin: 0 (-$cgt-breadcrumb-padding-between-item);
  padding: 0;

  li {
    padding-right: $cgt-breadcrumb-padding-between-item;
    list-style-type: none;
    flex-shrink: 0;

    &:not(:first-child)::before {
      @include typography-style();
      display: inline-block;
      content: '/';
    }
  }

  a,
  span {
    @include typography-style();
    margin-left: $cgt-breadcrumb-padding-between-item;
    padding-bottom: 4px;
    text-decoration: none;
    transition-duration: $global-animation-time;
    transition-property: color, border-color;
  }

  a {
    border-bottom: 1px solid transparent;

    &:hover {
      border-color: $cgt-breadcrumb-text-color-hover;
      color: $cgt-breadcrumb-text-color-hover;
    }
  }
}
