.arsenal {
    background-color: $inner-global-background-section-ui;
    padding-top: 70px;
    padding-bottom: 70px;
    overflow: hidden;

    @include mobile-s {
        padding-top: 112px;
        padding-bottom: 112px;
    }

    @include tablet-m {
        padding-top: 80px;
        padding-bottom: 80px;
        position: relative;
    }

    &__text {
        margin-bottom: 34px;
        position: relative;

        h3 {
            line-height: 1.33;

            @include tablet-m {
                margin-bottom: 12px;
            }
        }

        @include tablet-m {
            display: flex;
            flex-direction: column;
            justify-content: center;
            margin-bottom: 0;

            p {
                margin-top: 0;
            }
        }
    }

    &__stat {
        position: relative;
        z-index: 1;
        line-height: 0;

        &:nth-child(even) {
            text-align: right;

            @include mobile-s {
                margin-right: 95px;

                .stat {
                    border-radius: 0;
                    border-top-left-radius: 40px;
                    border-bottom-right-radius: 40px;
                }
            }

            @include tablet-m {
                text-align: left;
                margin-right: 0;
            }
        }

        &:nth-child(odd) {

            @include mobile-s {
                text-align: right;
            }
        }

        &:not(:last-child) {
            margin-bottom: 20px;

            @include tablet-m {
                margin-bottom: 24px;
            }
        }

        .stat {
            display: inline-block;

            @include tablet-m {
                width: 80%;
            }
        }
    }

    &__image {
        margin-left: -$inner-global-margin-container-xs;
        margin-right: -$inner-global-margin-container-xs;
        margin-top: -70px;
        transform: translateY(27%);

        @include mobile-s {
            width: calc(100% + 40px);
            margin-left: -20px;
            margin-right: -20px;
            order: 1;
            margin: 0 -20px;
            transform: none;
            flex-shrink: 0;
        }

        @include tablet-m {
            width: 880px;
            margin: 0;
        }

        img {
            @include mobile-s {
                max-width: none;
                width: calc(100% + 100px);
            }

            @include tablet-m {
                max-width: 100%;
                width: auto;
            }
        }
    }

    &__stats {

        @include mobile-s {
            display: flex;
            align-items: center;
        }
    }

    &__stat-list {

        @include mobile-s {
            margin-right: -100%;
            width: 100%;
        }

        @include tablet-m {
            flex-shrink: 0;
            width: 63%;
            margin-right: -220px;
        }
    }

    &__decoration-dots {
        position: absolute;
        bottom: -45px;
        right: 0;
        z-index: 0;
        touch-action: none;
        pointer-events: none;

        @include mobile-s {
            bottom: -250px;
        }

        @include tablet-m {
            display: none;
        }

        &--outer {

            @include tablet-m {
                display: block;
                top: 50%;
                margin-top: -98px;
                left: 0;
                right: auto;
                bottom: auto;
            }
        }
    }

    .description__link {
        position: relative;
        z-index: 2;
    }
}