.transforming-retail {
    padding-top: 66px;
    padding-bottom: 73px;
    position: relative;
    z-index: 1;

    @include mobile-s {
        padding-top: 78px;
        padding-bottom: 112px;
    }

    @include tablet-m {
        padding-top: 56px;
        padding-bottom: 56px;
    }

    &__video {
        line-height: 0;

        video {
            height: auto;
            width: 100%;
        }
    }

    &__title {
        margin-bottom: 42px;

        @include mobile-s {
            margin-bottom: 56px;
        }

        @include tablet-m {
            margin-bottom: 24px;
        }
    }

    &__decoration-dots {
        position: absolute;
        z-index: -1;
        display: none;

        @include mobile-s {
            display: block;
        }

        &:nth-child(1) {

            @include mobile-s {
                top: -140px;
                left: -160px;
            }

            @include tablet-m {
                left: 4%;
                top: 40px;
            }
        }

        &:nth-child(2) {
            @include mobile-s {
                bottom: -90px;
                right: 0;
            }

            @include tablet-m {
                right: 4%;
                bottom: 132px;
            }
        }
    }

    &__mobile {
        @include mobile-s {
            display: none;
        }
    }

    &__tablet {
        display: none;

        @include mobile-s {
            display: block;
        }

        @include tablet-m {
            display: none;
        }
    }

    &__desktop {
        display: none;

        @include tablet-m {
            display: block;
        }
    }
}