$hamburger-icon-color: $cgt-color-brand-primary-600;

.hamburger {
    display: inline-block;
    padding: 10px;

    &__wrap {
        height: 17px;
        display: flex;
        align-items: center;
    }

    &__icon {
        display: block;
        width: 23px;
        height: 2px;
        position: relative;

        span {
            display: block;
            position: absolute;
            height: 2px;
            width: 100%;
            border-radius: 2px;
            background-color: $hamburger-icon-color;
            opacity: 1;
            left: 0;
            transform: rotate(0deg);
            transition: $global-animation-time; // from cgtui.

            &:nth-child(1) {
                top: -8px;

                .menu-open & {
                    top: 0px;
                    transform: rotate(135deg);
                }
            }

            &:nth-child(2) {
                top: 0px;

                .menu-open & {
                    opacity: 0;
                    left: -60px;
                }
            }

            &:nth-child(3) {
                bottom: -8px;

                .menu-open & {
                    bottom: 0px;
                    transform: rotate(-135deg);
                }
            }
        }
    }
}