.modal {
    display: none;

    .cgt-modal {
        width: 100%;
        background-color: #000;
        margin: 0;
        height: calc(100% - 40px);
        margin-top: 40px;

        @include tablet-m {
            background-color: transparent;
            height: auto;
        }
    }

    &.cgt-modal--background {
        height: 100%;
        background-color: #000;

        @include tablet-m {
            background-color: rgba(#1b1c1f, 0.5);
        }
    }

    .cgt-modal--container {
        height: 100%;
        padding: 0;

        @include tablet-m {
            display: flex;
            align-items: center;
            padding: 0 40px;
        }
    }

    iframe {
        width: 100%;
        height: 100%;

        @include tablet-m {
            height: 590px;
        }
    }

    .cgt-modal__close {
        right: 0;
    }
}