// Name:            Select
// Description:     Styles for select
//
// Component:       `cgt-select`
//
// Sub-objects:     `cgt-select__wrapper`
//
// Modifiers:       `cgt-select--icon`
// `cgt-select--multiple`
// `cgt-select--disabled`
//
// States:          `is-open`
//
// Variables
// ========================================
$cgt-select-border-color:               $cgt-color-ui-400;
$cgt-select-back-color:                 $cgt-color-ui-100;
$cgt-select-color:                      $cgt-color-ui-800;
$cgt-select-arrow-color:                $cgt-color-ui-700;
$cgt-select-label-color:                $cgt-color-ui-600;
$cgt-select-back-color-option-checked:  $cgt-color-ui-300;
$cgt-select-placeholder-color:          $cgt-color-ui-500;
$cgt-select-menu-shadow-color:          $cgt-color-ui-400;
$cgt-select-check-theme:                $cgt-color-brand-primary-600;
$cgt-select-check-icon-color:           $cgt-color-ui-100;
$cgt-select-back-color-hover:           $cgt-color-ui-400;
$cgt-select-disabled-theme:             $cgt-color-ui-400;

@mixin cgt-select-style-value {
  font-size: $global-text-size-medium !important;
  color: $cgt-select-color !important;
  font-weight: normal !important;
}

.cgt-select {
  margin-bottom: 16px;

  &--not-spaced {
    margin-bottom: 0;
  }

  select,
  &__control {
    @include cgt-select-style-value();
    width: 100% !important;
    height: 44px !important;
    border-width: 1px;
    border-style: solid;
    border-radius: 1px !important;
    border-color: $cgt-select-border-color !important;
    background-color: $cgt-select-back-color !important;
    padding-right: 16px !important;
    padding-left: 16px !important;
    -moz-appearance: none !important;
    -webkit-appearance: none !important;
    appearance: none !important;
    transition-property: background-color;
    transition-duration: $global-animation-time;
    cursor: pointer;

    &:focus {
      @include global-focus();
      outline: none;
    }

    &:not(.cgt-select__control--is-disabled):hover {
      background-color: $cgt-select-back-color-hover !important;
    }

    &:disabled {
      color: $cgt-select-disabled-theme !important;
      cursor: not-allowed !important;

      &:hover {
        background-color: $cgt-select-back-color !important;
      }
    }
  }

  &__wrapper {
    position: relative;
  }

  label {
    color: $cgt-select-label-color;
    font-size: $global-text-size-small;
    font-weight: 600;
    line-height: normal;
  }

  .fa-angle-down {
    position: absolute;
    top: 14px;
    right: 16px;
    color: $cgt-select-arrow-color;
    font-size: 16px;
    touch-action: none;
    pointer-events: none;
  }

  &__indicator-separator {
    display: none;
  }

  &__value-container {
    padding: 0 !important;
    text-overflow: ellipsis !important;

    &--is-multi {
      display: block !important;
      position: relative;
      overflow: hidden;
      white-space: nowrap !important;
      flex-wrap: nowrap !important;
    }
  }

  &__indicators {
    margin-right: -16px;
  }

  &__dropdown-indicator {
    padding: 10px 12px 8px !important;
    font-size: 0;
    line-height: 0;
    transition: transform $global-animation-time !important;

    &::before {
      @include global-use-icon-fontawesome(400);
      color: $cgt-select-arrow-color;
      font-size: $global-text-size-medium;
      line-height: 10px;
      content: '\f107';
    }

    svg {
      display: none;
    }
  }

  &--icon {

    .cgt-select__wrapper {

      > i:not(.fa-angle-down) {
        position: absolute;
        top: 14px;
        left: 16px;
        z-index: 10;
      }
    }

    select,
    .cgt-select__control {
      padding-left: 44px !important;
    }
  }

  &--multiple {

    select {
      height: auto !important;
      max-height: 220px !important;
      padding: 0 !important;
    }

    .fa-angle-down {
      display: none;
    }

    option {
      padding: 12px 16px;
    }

    .cgt-select__option {

      &::before {
        @include global-checkbox-uncheck();
        margin-top: 5px;
      }

      &--is-selected {
        background-color: transparent !important;
      }

      &--is-selected::before {
        @include global-use-icon-fontawesome(900);
        border-color: $cgt-select-check-theme;
        background-color: $cgt-select-check-theme;
        color: $cgt-select-check-icon-color;
        font-size: 12px;
        line-height: 15px;
        text-align: center;
        content: '\f00c';
      }
    }
  }

  &--custom & {

    &__menu {
      display: none;
    }
  }

  &--disabled {
    label,
    i,
    .fa-angle-down {
      color: $cgt-select-disabled-theme;
    }

    .cgt-select__wrapper {
      cursor: not-allowed;
    }
  }

  &--error {
    margin-bottom: 24px;

    select {
      border-color: $cgt-input-theme-error !important;
    }

    .cgt-input__error-message {
      display: block;

      &::first-letter {
        text-transform: uppercase;
      }
    }
  }

  &__control {
    display: flex;
    position: relative;
    min-height: 38px;
    outline: 0 !important;
    align-items: center;
    justify-content: space-between;
    transition: all $global-animation-time;

    &--menu-is-open {
      .cgt-select__dropdown-indicator {
        transform: rotate(-180deg);
      }
    }

    &--is-focused {
      border-color: $border-color-focus !important;
      box-shadow: 0 0 0 4px $shadow-focus !important;
    }

    &--is-disabled {
      cursor: not-allowed !important;

      .cgt-select__placeholder,
      .cgt-select__dropdown-indicator::before {
        color: $cgt-select-disabled-theme;
      }
    }
  }

  &__single-value {
    @include cgt-select-style-value();
  }

  &__multi-value {
    display: inline-block !important;
    margin: 0 !important;
    background: none !important;
    vertical-align: middle !important;
    flex-shrink: 0;

    &:not(:nth-last-child(2)) {
      .cgt-select__multi-value__label::after {
        padding-right: 3px;
        content: ', ';
      }
    }

    &__label {
      @include cgt-select-style-value();
      padding: 0 !important;
      text-overflow: ellipsis;
      white-space: nowrap;
      box-sizing: border-box;
    }

    &__remove {
      display: none !important;
    }
  }

  &__placeholder {
    color: $cgt-select-placeholder-color;
  }

  &__menu {
    margin: 4px 0 !important;
    border: solid 1px $cgt-select-border-color !important;
    border-radius: 0 !important;
    box-shadow: 0 2px 6px 0 $cgt-select-menu-shadow-color !important;
  }

  &__option {
    padding: 12px 16px !important;
    color: $cgt-select-color !important;
    font-weight: normal !important;
    font-size: $global-text-size-medium !important;
    cursor: pointer !important;

    &:hover,
    &--is-focused,
    &--is-selected {
      background-color: $cgt-select-back-color-option-checked !important;
    }
  }

  &__select {
    position: relative;
  }

  &__dummy-input {
    position: relative;
    left: -100px;
    opacity: 0;
    width: 1px;
    outline: 0;
    border: 0;
    background: 0;
    padding: 0;
    color: transparent;
    font-size: inherit;
    transform: scale(0);
  }

  &.is-open {

    .cgt-select__menu {
      display: block;
    }

    .cgt-select__dropdown-indicator {
      transform: rotate(-180deg);
    }
  }

  .cgt-search input {
    width: calc(100% + 2px);
    margin: -1px -1px 0;
  }
}
