// Variables
// ===================================
$cgt-toggle-back-switch-check:            $cgt-color-brand-primary-600;
$cgt-toggle-back-switch-uncheck:          $cgt-color-ui-700;
$cgt-toggle-flag-color:                   $cgt-color-ui-100;
$cgt-toggle-label-color:                  $cgt-color-ui-800;
$cgt-toggle-disabled-theme:               $cgt-color-ui-400;

.cgt-toggle {
  display: inline-flex;
  align-items: flex-start;

  input {
    display: none;
  }

  &__switch {
    display: inline-block;
    position: relative;
    width: 32px;
    height: 16px;
    margin-top: 2px;
    margin-right: 12px;
    border-radius: 8px;
    background-color: $cgt-toggle-back-switch-uncheck;
    cursor: pointer;
    transition-property: transform, background-color;
    transition-duration: $global-animation-time;

    &::before {
      display: inline-block;
      position: absolute;
      top: 1px;
      left: 1px;
      width: 14px;
      height: 14px;
      border-radius: 50%;
      background-color: $cgt-toggle-flag-color;
      transform: translateX(16px);
      transition-property: transform;
      transition-duration: $global-animation-time;
      content: '';
    }
  }

  &__text {
    color: $cgt-toggle-label-color;
    line-height: 1.25;
    font-weight: normal;
    font-size: $global-text-size-medium;
    cursor: pointer;
  }

  input:checked + &__switch {
    background-color: $cgt-toggle-back-switch-check;

    &::before {
      transform: translateX(0);
    }
  }

  input:disabled {

    + .cgt-toggle__switch {
      background-color: $cgt-toggle-disabled-theme;
      cursor: default;
    }

    ~ .cgt-toggle__text {
      color: $cgt-toggle-disabled-theme;
      cursor: default;
    }
  }
}
