$nav-background-link-sub-nav-hover: #f9fdfe;

.nav {

    a {
        text-decoration: none;
    }

    &__list,
    &__list-sub-nav {
        padding: 0;
        margin: 0;
    }

    &__list {

        @include mobile-s {
            display: flex;
        }
    }

    &__list-sub-nav {
        display: none;

        @include mobile-s {
            position: absolute;
            width: 200px;
            top: 100%;
            left: -20px;
            box-shadow: 0 4px 12px 0 rgba(179, 181, 183, 0.16);
            background-color: $inner-color-white;
            transition-property: opacity;
            transition-duration: $global-animation-time;
            transition-delay: 0s;

            .is-device-desktop & {
                opacity: 0;
                display: block;
                touch-action: none;
                pointer-events: none;
            }
        }
    }

    &__item,
    &__item-sub-nav {
        list-style: none;
    }

    &__item {

        @include mobile-s {
            margin: 0 10px;
            position: relative;
        }

        @media (min-width: 576px) and (max-width: 767px) {
            margin: 0 5px;
        }

        @media (min-width: 1120px) {
            margin: 0 24px;
        }

        .is-device-desktop &:hover {

            @include mobile-s {
                .nav {

                    &__list-sub-nav {
                        transition-delay: 0.1s;
                        opacity: 1;
                        touch-action: auto;
                        pointer-events: all;
                    }

                    &__link {
                        border-bottom-color: $cgt-color-ui-700;
                        transition-delay: 0.1s;
                    }
                }
            }
        }
    }

    &__link {
        display: block;
        padding: 24px 40px;

        @include mobile-s {
            padding-left: 0;
            padding-right: 0;
            border-bottom: 2px solid transparent;
            transition: border-color $global-animation-time 0s;
        }

        @media (min-width: 576px) and (max-width: 767px) {
            font-size: 12px;
        }

        &.dropdown-open {
            font-weight: 600;
            color: $cgt-color-ui-800;

            @include mobile-s {
                border-bottom-color: $cgt-color-ui-700;
            }
        }
    }

    &__link-sub-nav {
        display: block;
        padding: 16px 60px;
        transition: background-color $global-animation-time;
        font-weight: normal;

        &:hover {
            background-color: $nav-background-link-sub-nav-hover;
            font-weight: 600;
        }

        @include mobile-s {
            padding: 16px 9px 16px 20px;
        }
    }

    &__arrow {
        transition: transform $global-animation-time;
        margin-left: 9px;
        vertical-align: middle;

        .dropdown-open & {
            vertical-align: middle;
            margin-left: 9px;
            transform: rotate(-180deg);
        }
    }
}