// Name:            Heading
// Description:     Styles for headings
//
// Component:       `cgt-heading-*`
//
// Modifiers:
// `cgt-heading-h1--medium` for font-weight
// `cgt-heading-h1--large` for font-weight
// `cgt-heading-h2--medium` for font-weight
// `cgt-heading-h2--large` for font-weight
// `cgt-heading-h3--medium` for font-weight
// `cgt-heading-h3--large` for font-weight
// `cgt-heading--dark-bg` for text color with dark background
// ========================================================================
//
// Variables
// ========================================================================
//
// Font
// ========================================================================
$cgt-heading-h1-font-size-mobile: 32px;
$cgt-heading-h1-font-size-desktop: 52px;

$cgt-heading-h2-font-size-mobile: 24px;
$cgt-heading-h2-font-size-desktop: 44px;

$cgt-heading-h3-font-size-mobile: 24px;
$cgt-heading-h3-font-size-desktop: 32px;

$cgt-heading-h4-font-size-mobile: 20px;
$cgt-heading-h4-font-size-desktop: 24px;

$cgt-heading-h5-font-size-mobile: 16px;
$cgt-heading-h5-font-size-desktop: 20px;

$cgt-heading-h6-font-size-mobile: 16px;
//
// Margins
// ========================================================================
$cgt-heading-margin-bottom: 16px;
$cgt-heading-margin-top: 16px;
$cgt-heading-margin-top-first-child: 32px;

.cgt-heading {

  &-h1 {
    font-size: $cgt-heading-h1-font-size-mobile;
    font-weight: normal;
    letter-spacing: -0.1px;
    line-height: 1.38;

    @media (min-width: $breakpoint-m) {
      font-size: $cgt-heading-h1-font-size-desktop;
      letter-spacing: -0.3px;
      line-height: 1.31;
    }
  }

  &-h1--medium {
    font-weight: 600;
  }

  &-h1--large {
    font-weight: bold;
  }

  &-h2 {
    font-size: $cgt-heading-h2-font-size-mobile;
    font-weight: 600;
    line-height: 1.83;

    @media (min-width: $breakpoint-m) {
      font-size: $cgt-heading-h2-font-size-desktop;
      font-weight: normal;
      letter-spacing: -0.2px;
      line-height: 1.45;
    }
  }

  &-h2--medium {
    @media (min-width: $breakpoint-m) {
      font-weight: 600;
    }
  }

  &-h2--large {
    @media (min-width: $breakpoint-m) {
      font-weight: bold;
    }
  }

  &-h3 {
    font-size: $cgt-heading-h3-font-size-mobile;
    font-weight: 600;
    line-height: 1.83;

    @media (min-width: $breakpoint-m) {
      font-size: $cgt-heading-h3-font-size-desktop;
      font-weight: normal;
      letter-spacing: -0.1px;
      line-height: 1.38;
    }
  }

  &-h3--medium {

    @media (min-width: $breakpoint-m) {
      font-weight: 600;
    }
  }

  &-h3--large {

    @media (min-width: $breakpoint-m) {
      font-weight: bold;
    }
  }

  &-h4 {
    font-size: $cgt-heading-h4-font-size-mobile;
    font-weight: 600;
    line-height: 1.4;

    @media (min-width: $breakpoint-m) {
      font-size: $cgt-heading-h4-font-size-desktop;
      line-height: 1.83;
    }
  }

  &-h5 {
    font-size: $cgt-heading-h5-font-size-mobile;
    font-weight: 600;
    line-height: 1.5;

    @media (min-width: $breakpoint-m) {
      font-size: $cgt-heading-h5-font-size-desktop;
      line-height: 1.4;
    }
  }

  &-h6 {
    font-size: $cgt-heading-h6-font-size-mobile;
    font-weight: 600;
    line-height: 1.5;
  }

  &-h1,
  &-h2,
  &-h3,
  &-h4,
  &-h5,
  &-h6 {
    margin: 0;
    color: $cgt-heading-primary;
  }

  &--dark-bg {
    color: $cgt-heading-primary-dark-bg;
  }
}
