// Name:            Spinner
// Description:     Styles for spinner
//
// Component:       `cgt-spinner`
//
// Sub-objects:     ``
//
// Modifiers:       `cgt-spinner--small`
//
// States:          ``

// Variables
// ====================================================
$cgt-spinner-size-large:                              44px;
$cgt-spinner-size-small:                              32px;
$cgt-spinner-width-border:                            4px;
$cgt-spinner-animation-time:                          1s;

.cgt-spinner {
  display: inline-block;
  width: $cgt-spinner-size-large;
  height: $cgt-spinner-size-large;
  border: $cgt-spinner-width-border solid $global-loader-rail-color;
  border-left: $cgt-spinner-width-border solid $global-loader-track-color;
  border-radius: 50%;
  animation: cgt-spinner $cgt-spinner-animation-time infinite linear;

  &--small {
    width: $cgt-spinner-size-small;
    height: $cgt-spinner-size-small;
  }
}

@keyframes cgt-spinner {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}
