.photo-slider {

    &__image-wrap {

        img {
            border-radius: 8px;
            box-shadow: 0 12px 24px 0 rgba(5, 37, 73, 0.08);
            width: 100%;
        }
    }

    &__slide {
        text-align: center;
    }

    &__pagination {
        margin-top: 22px;

        @include mobile-s {
            margin-top: 28px;
        }
    }

    &__button {
        display: flex;
        justify-content: space-between;
        transform: translateY(-65%);
        touch-action: none;
        pointer-events: none;
        max-width: 204px;
        margin: 0 auto;

        @include mobile-s {
            max-width: 250px;
        }
    }
}