// Stylization for https://react-day-picker.js.org

// Variables
// =======================================
$cgt-date-picker-shadow:                $cgt-color-ui-400;
$cgt-date-picker-color:                 $cgt-color-ui-700;
$cgt-date-picker-color-today:           $cgt-color-brand-primary-600;
$cgt-date-picker-color-outside:         $cgt-color-ui-400;
$cgt-date-picker-arrow-color:           $cgt-color-ui-500;
$cgt-date-picker-arrow-color-hover:     $cgt-color-ui-800;
$cgt-date-picker-caption-color:         $cgt-color-ui-800;
$cgt-date-picker-back-color-day-hover:  $cgt-color-ui-300;

// scss-lint:disable SelectorFormat, QualifyingElement
.DayPicker {
  display: inline-block;
  position: absolute;
  top: calc(100% + 4px);
  left: 0;
  z-index: 100;
  border: 1px solid $border-color-primary;
  background-color: $cgt-color-ui-100;
  box-shadow: 0 2px 6px 0 $cgt-date-picker-shadow;

  &-wrapper {
    position: relative;
    padding-bottom: 16px;
    user-select: none;
    flex-direction: row;
  }

  &-Months {
    display: flex;
    flex-wrap: wrap;
    justify-content: center;
  }

  &-Month {
    display: table;
    margin: 0 16px;
    margin-top: 16px;
    border-spacing: 0;
    border-collapse: collapse;
    user-select: none;
  }

  &-Weekdays {
    display: table-header-group;
    margin-top: 16px;
  }

  &-WeekdaysRow {
    display: table-row;
  }

  &-Weekday,
  &-Day {
    display: table-cell;
    padding: 6px 8px;
    color: $cgt-date-picker-color;
    line-height: 24px;
    font-size: $global-text-size-medium;
    text-align: center;

    &:nth-child(6),
    &:nth-child(7) {
      font-weight: 600;
    }
  }

  &-Weekday abbr[title] {
    border-bottom: 0;
    text-decoration: none;
  }

  &-Day {
    display: table-cell;
    border-radius: 1px;
    vertical-align: middle;
    text-align: center;
    cursor: pointer;

    &:focus {
      outline: none;
    }

    &--today {
      color: $cgt-date-picker-color-today;
      font-weight: 600;
    }

    &--selected,
    &:hover {
      background-color: $cgt-color-ui-300;
    }

    &--outside {
      color: $cgt-date-picker-color-outside !important;
      cursor: default;
    }
  }

  &-Body {
    display: table-row-group;
  }

  &-Week {
    display: table-row;
  }

  &-NavButton {
    display: inline-block;
    position: absolute;
    top: 16px;
    right: 24px;
    left: auto;
    width: 24px;
    height: 24px;
    margin-top: 2px;
    color: $cgt-date-picker-arrow-color;
    vertical-align: middle;
    line-height: 24px;
    text-align: center;
    cursor: pointer;
    transition-property: color;
    transition-duration: $global-animation-time;

    &::before {
      @include global-use-icon-fontawesome(400);
    }

    &:hover {
      color: $cgt-date-picker-arrow-color-hover;
    }

    &:focus {
      outline: none !important;
    }

    &--prev {
      right: auto;
      left: 16px;

      &::before {
        content: '\f104';
      }
    }

    &--next {
      right: 16px;

      &::before {
        content: '\f105';
      }
    }

    &--interactionDisabled {
      display: none;

      .DayPicker-Day {
        cursor: default;
      }
    }
  }

  &-Caption {
    display: table-caption;
    margin-bottom: 8px;
    padding: 0 8px;
    color: $cgt-date-picker-caption-color;
    font-size: $global-text-size-medium;
    font-weight: normal;
    text-align: center;
  }

  &-Months,
  &-wrapper {

    &:focus {
      outline: none !important;
    }
  }
}

.cgt-input__field-wrap + div {
  outline: none !important;
}
