.photorealistic-lifestyle {
    padding: 70px 0;
    position: relative;
    overflow: hidden;

    @include mobile-s {
        padding: 112px 0;
    }

    @include tablet-m {
        overflow: visible;
    }

    &:first-child {
        padding-top: 40px;

        @include mobile-s {
            padding-top: 64px;
        }

        @include tablet-m {
            padding-top: 76px;
        }
    }

    &__text {
        margin-bottom: 22px;

        @include mobile-s {
            margin-bottom: 28px;
        }

        @include desktop-xl {
            padding-right: 90px !important;
        }
    }

    &__wrap {
        position: relative;
        z-index: 2;

        @include desktop-xl {
            margin-right: -90px;
        }
    }

    &__decoration-dots {
        position: absolute;
        z-index: 1;

        &:nth-child(1) {
            right: 20px;
            top: -38px;

            @include mobile-s {
                display: none;
            }

            @include tablet-m {
                display: block;
                left: 4%;
                top: 25px;
            }
        }

        &:nth-child(2) {
            top: 30%;
            right: -40px;

            @include mobile-s {
                top: 20%;
                right: -113px;
            }

            @include tablet-m {
                top: auto;
                bottom: -85px;
                right: 4%;
            }
        }
    }
}