.person {
    display: flex;
    align-items: center;

    &__photo-wrap {
        flex-shrink: 0;
        width: 56px;
        height: 56px;
        line-height: 0;
        font-size: 0;
        margin-right: 16px;

        img {
            width: 100%;
            height: 100%;
            object-fit: cover;
            border-radius: 50%;
            display: inline-block;
            overflow: hidden;
        }
    }

    &__about {
        line-height: 1.67;

        & > * {
            margin: 0;

            &:first-child {
                font-weight: bold;
                color: $cgt-color-ui-800;

                @include mobile-s {
                    font-weight: 600;
                }
            }
        }
    }
}