// Variables
// ===================================
$cgt-radio-label-color:               $cgt-color-ui-800;
$cgt-radio-border-color-hover:        $cgt-color-brand-primary-600;
$cgt-radio-border-color:              $cgt-color-ui-500;
$cgt-radio-check-color:               $cgt-color-ui-100;
$cgt-radio-check-theme:               $cgt-color-brand-primary-600;
$cgt-radio-disabled-theme:            $cgt-color-ui-400;

.cgt-radio {
  display: inline-flex;
  position: relative;
  line-height: 0;
  font-size: 0;
  align-items: flex-start;

  &:hover {

    .cgt-radio__input + .cgt-radio__text::before {
      border-color: $cgt-radio-border-color-hover;
    }
  }

  &__input {
    display: none;

    + .cgt-radio__text {
      color: $cgt-radio-label-color;
      line-height: 1.25;
      font-size: $global-text-size-medium;
      cursor: pointer;

      &::before {
        display: inline-block;
        width: 16px;
        height: 16px;
        margin-top: 2px;
        margin-right: 12px;
        border: 1px solid $cgt-radio-border-color;
        border-radius: 50%;
        content: '';
        transition-property: border-color, background-color;
        transition-duration: $global-animation-time;
        vertical-align: top;
      }

      &::after {
        display: inline-block;
        position: absolute;
        top: 6px;
        left: 4px;
        opacity: 0;
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background-color: $cgt-radio-check-color;
        content: '';
        transition-property: opacity;
        transition-duration: $global-animation-time;
      }
    }
  }

  &__input:checked + &__text {

    &::before {
      border-color: $cgt-radio-check-theme;
      background-color: $cgt-radio-check-theme;
    }

    &::after {
      opacity: 1;
    }
  }

  &__input:disabled + &__text {
    color: $cgt-radio-disabled-theme;
    cursor: default;

    &::before {
      border-color: $cgt-radio-disabled-theme !important;
    }
  }
}
