// Variables
// =================================
$cgt-switcher-border-radius:                1px;
$cgt-switcher-text-color:                   $cgt-color-ui-600;
$cgt-switcher-text-color-checked:           $cgt-color-ui-100;
$cgt-switcher-background-color:             $cgt-color-ui-100;
$cgt-switcher-background-color-hover:       $cgt-color-ui-400;
$cgt-switcher-background-color-checked:     $cgt-color-brand-primary-600;
$cgt-switcher-border-color:                 $cgt-color-ui-400;
$cgt-switcher-border-color-checked:         $cgt-color-brand-primary-600;

.cgt-switcher {
  display: flex;
  overflow-x: auto;

  .cgt-switcher__text {
    display: inline-block;
    border: 1px solid $cgt-switcher-border-color;
    background-color: $cgt-switcher-background-color;
    padding: 12px 24px;
    color: $cgt-switcher-text-color;
    cursor: pointer;
    font-size: $global-text-size-medium;
    font-weight: normal;
    line-height: normal;
    transition-duration: $global-animation-time;
    transition-property: background-color, color;

    &:hover {
      background-color: $cgt-switcher-background-color-hover;
    }
  }

  &__label {
    flex-shrink: 0;

    &:not(:first-child) {
      margin-left: -1px;
    }

    &:first-child {
      .cgt-switcher__text {
        border-bottom-left-radius: $cgt-switcher-border-radius;
        border-top-left-radius: $cgt-switcher-border-radius;
     }
    }

    &:last-child {
      .cgt-switcher__text {
        border-bottom-right-radius: $cgt-switcher-border-radius;
        border-top-right-radius: $cgt-switcher-border-radius;
      }
    }
  }

  &__input {
    display: none;
  }

  &__input:checked + &__text {
    border-color: $cgt-switcher-border-color-checked;
    background-color: $cgt-switcher-background-color-checked;
    color: $cgt-switcher-text-color-checked;
  }
}
