.overview-arsenal {
    overflow: hidden;
    position: relative;

    @include tablet {
        padding-bottom: 112px;
    }

    &:first-child {
        padding-top: 40px;

        @include tablet {
            padding-top: 64px;
        }

        @include tablet-m {
            padding-top: 102px;
        }
    }

    &__text {
        margin-bottom: 80px;

        @include tablet {
            margin-bottom: 56px;
        }

        @include tablet-m {
            margin-bottom: 0;
        }
    }

    &__wrap {

        @include tablet-m {
            align-items: center;
        }

        .link {
            font-weight: 600;
            cursor: pointer;

            @include mobile-s {
                font-weight: 600;
            }

            .fa-play {
                font-size: 10px;
            }
        }
    }

    &__decoration-dots {
        display: none;

        @include mobile-s {
            display: block;
            position: absolute;
            z-index: 1;
            bottom: 30px;
            right: -60px;
        }

        @include tablet-m {
            bottom: 140px;
            right: auto;
            left: 82%;
        }
    }

    .container-fixed {
        position: relative;
        z-index: 2;
    }
}