.play {
    display: inline-flex;
    align-items: center;
    justify-content: center;
    width: 64px;
    height: 64px;
    position: relative;

    &::before,
    &::after {
        content: '';
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        border-radius: 50%;
        display: inline-block;
        overflow: hidden;
        flex-shrink: 0;
        position: absolute;
        z-index: 1;
    }

    &::before {
        width: 100%;
        height: 100%;
        border: 1px solid #f9fdfe;
    }

    &::after {
        width: 51px;
        height: 51px;
        background-color: #fff;
    }

    &__icon {
        position: relative;
        z-index: 2;
    }
}