.application-presentation {
    position: relative;

    &__wrap {
        align-items: center;
        position: relative;
        z-index: 3;
    }

    &__text {
        margin-bottom: 22px;

        @include mobile-s {
            margin-bottom: 28px;
        }

        @include tablet-m {
            margin-bottom: 0;
        }
    }

    &__image {

        img {
            width: 100%;
            max-width: none;
            overflow: hidden;
            border-top-right-radius: 40px;
            border-bottom-left-radius: 40px;
            box-shadow: 8px 8px 16px 0 rgba(5, 70, 75, 0.08);

            @include mobile-s {
                box-shadow: 0 24px 48px 0 rgba(5, 70, 75, 0.08);
            }

            @include desktop-xl {
                width: 665px;
            }
        }
    }

    &__decoration-dots {
        position: absolute;
        z-index: 1;
    }

    .container-fixed {
        padding-top: 40px;
        padding-bottom: 70px;
        position: relative;
        overflow: hidden;

        @include mobile-s {
            padding-top: 64px;
            padding-bottom: 112px;
        }

        @include tablet-m {
            overflow: visible;
        }
    }

    &--furniture > &__decoration-dots {

        &:nth-child(1) {
            top: -40px;
            right: 25px;

            @include mobile-s {
                top: auto;
                bottom: -140px;
                right: 117px;
            }

            @include tablet-m {
                top: 40px;
                left: 4%;
                bottom: auto;
                right: auto;
            }
        }
    }

    &--furniture .container-fixed > &__decoration-dots {

        &:nth-child(1) {
            top: 50%;
            transform: translateY(-50%);
            right: -40px;

            @include tablet-m {
                right: auto;
                left: 62%;
                transform: translateX(-50%);
                bottom: 63px;
                top: auto;
            }
        }

        &:nth-child(2) {
            bottom: 50px;
            right: -30px;

            @include mobile-s {
                display: none;
            }
        }

        &:nth-child(3) {
            bottom: 100px;
            left: -40px;

            @include mobile-s {
                display: none;
            }
        }
    }

    &--fashion {
        overflow: hidden;
    }

    &--fashion > &__decoration-dots {

        &:nth-child(1) {
            top: -35px;
            right: 35px;

            @include mobile-s {
                top: 44%;
                transform: translateY(-50%);
                right: -20px;
            }

            @include tablet-m {
                top: 40px;
                right: auto;
                left: 4%;
                transform: none;
            }
        }
    }

    &--fashion .container-fixed > &__decoration-dots {

        &:nth-child(1) {
            bottom: 23%;
            right: -71px;
            z-index: 4;

            @include mobile-s {
                display: none;
            }

            @include tablet-m {
                bottom: 50%;
                transform: translateY(50%);
                z-index: 1;
                right: -75px;
                display: block;
            }

            @include desktop-xl {
                right: -284px;
            }
        }

        &:nth-child(2) {
            bottom: 10px;
            right: -12px;

            @include mobile-s {
                display: none;
            }
        }

        &:nth-child(3) {
            display: none;
        }
    }

    &--home-appliances > &__decoration-dots {

        &:nth-child(1) {
            top: -35px;
            right: 35px;

            @include mobile-s {
                top: auto;
                bottom: -90px;
                right: 6px;
            }

            @include tablet-m {
                top: 40px;
                bottom: auto;
                right: auto;
                left: 4%;
            }
        }
    }

    &--home-appliances .container-fixed > &__decoration-dots {

        &:nth-child(1) {
            top: 51%;
            transform: translateY(-50%);
            right: -38px;

            @include mobile-s {
                right: auto;
                left: -148px;
                top: 45%;
            }

            @include tablet-m {
                left: auto;
                top: auto;
                bottom: 59px;
                transform: none;
                right: 135px;
            }

            @include desktop-xl {
                right: 30px;
            }
        }

        &:nth-child(2) {
            bottom: 51px;
            left: -40px;

            @include mobile-s {
                display: none;
            }
        }

        &:nth-child(3) {
            display: none;
        }
    }
}