.customers {
    padding: 40px 0;

    @include mobile-s {
        padding: 112px 0;
    }

    &__list {
        padding: 0;
        margin: 0;
        align-items: center;
        flex-wrap: nowrap;
        overflow-x: auto;
        overflow-y: hidden;
    }

    &__item {
        list-style-type: none;
        line-height: 0;

        a {
            display: inline-block;
        }
    }
}