.footer {
    padding-top: 52px;
    padding-bottom: 28px;

    @include tablet-m {
        padding-top: 64px;
        padding-bottom: 40px;
    }

    &__nav-item {
        margin-bottom: 44px;
    }

    &__logo {
        margin-bottom: 32px;

        @include tablet-m {
            margin-bottom: 0;
        }
    }

    &__additional-link {
        opacity: 0.6;
        text-decoration: none;
    }

    a.footer__additional-link {
        border-bottom: 1px solid transparent;
        transition-property: opacity, border-color;
        transition-duration: 0.3s;

        .is-device-desktop &:hover,
        &:active {
            opacity: 1;
            text-decoration: underline;
        }
    }

    &__additional-info {
        display: flex;
        justify-content: center;

        @include tablet-m {
            flex-grow: 1;
            max-width: 375px;
        }
    }

    &__additional-wrap {
        display: flex;
        justify-content: space-between;
        max-width: 375px;
        width: 100%;
    }

    &__social {
        margin-bottom: 32px;

        @include tablet-m {
            order: 1;
            margin-bottom: 0;
        }
    }

    &__nav-row {

        @include tablet-m {
            justify-content: space-between;
        }
    }

    &__nav {

        @include tablet-m {
            order: -1;
            margin-bottom: 16px;
        }
    }

    &__wrap {

        @include tablet-m {
            justify-content: space-between;
        }
    }
}