// Name:            Alert
// Description:     Component to create alert
//
// Component:       `cgt-alert`
//
// Modifiers:       ``

// Variables
// ============================================

.cgt-alert {
  display: flex;
  border-radius: 1px;
  padding: 12px 12px 12px 16px;
  font-size: $global-text-size-medium;
  line-height: 1.25;
  align-items: center;

  &__icon {
    margin-bottom: auto;
    padding: 4px 12px 0 0;
    color: inherit;
    font-size: 14px;
    line-height: 1;
    flex-shrink: 0;
  }

  &__message {
    padding-right: 12px;
    flex-grow: 1;
  }

  &__close {
    outline: none;
    border: 0;
    background: none;
    padding: 4px;
    color: inherit;
    line-height: 1;
    font-size: 14px;
    cursor: pointer;
    flex-shrink: 0;
  }

  a {
    color: inherit;
  }
}

// Name: notifyjs extensions

.notifyjs-custom-base {
  @extend .cgt-alert;

  .notifyjs-notification-text {
    @extend .cgt-alert__message;
  }

  .notification-icon {
    @extend .cgt-alert__icon;
    font-family: 'Font Awesome 5 Pro';
  }

  .notification-close-icon {
    @extend .cgt-alert__close;

    &::before {
      font-family: 'Font Awesome 5 Pro';
      content: fa-content($fa-var-times);
    }
  }
}

.notifyjs-custom-error {
  background-color: $cgt-color-utility-danger-200;
  color: $cgt-color-utility-danger-600;

  .notification-icon {
    &::before {
      font-family: 'Font Awesome 5 Pro';
      content: fa-content($fa-var-times-circle);
    }
  }

  .notification-close-icon {
    &::before {
      font-family: 'Font Awesome 5 Pro';
      -webkit-text-stroke: 2px $cgt-color-utility-danger-600;
    }
  }
}

.notifyjs-custom-success {
  background-color: $cgt-color-brand-primary-600;
  color: $cgt-color-ui-100;

  .notification-icon {
    &::before {
      font-family: 'Font Awesome 5 Pro';
      content: fa-content($fa-var-times-circle);
    }
  }

  .notification-close-icon {
    &::before {
      font-family: 'Font Awesome 5 Pro';
      content: fa-content($fa-var-times);
      -webkit-text-stroke: 2px $cgt-color-ui-100;
    }
  }
}
