// Name:            Text
// Description:     Utilities for text
//
// Component:       `cgt-text-*`
//
// Modifiers:
// `cgt-text-*--dark-bg` change text color for dark background
// `cgt-text--color-primary` any text with this color
// `cgt-text--color-secondary` any text with this color
// ========================================================================

.cgt-text {

  &-lead {
    color: $cgt-text-lead;
    font-size: $global-text-size-large;
    font-weight: normal;
    line-height: 1.5;

    &--dark-bg {
      color: $cgt-text-lead-dark-bg;
    }

    a {
      @extend .cgt-link;
      font-size: inherit;
      font-weight: inherit;
      line-height: inherit;
    }
  }

  &-default {
    color: $cgt-text-default;
    font-size: $global-text-size-medium;
    font-weight: normal;
    line-height: 1.75;

    &--dark-bg {
      color: $cgt-text-default-dark-bg;
    }

    a {
      @extend .cgt-link;
      font-size: inherit;
      font-weight: inherit;
      line-height: inherit;
    }
  }

  &-caption {
    color: $cgt-text-caption;
    font-size: $global-text-size-medium;
    font-weight: 600;
    line-height: 1.75;

    &--dark-bg {
      color: $cgt-text-caption-dark-bg;
    }

    a {
      @extend .cgt-link;
      font-size: inherit;
      font-weight: inherit;
      line-height: inherit;
    }
  }

  &-tiny {
    color: $cgt-text-tiny;
    font-size: $global-text-size-small;
    font-weight: normal;
    line-height: 1.67;

    &--dark-bg {
      color: $cgt-text-tiny-dark-bg;
    }

    a {
      @extend .cgt-link;
      font-size: inherit;
      font-weight: inherit;
      line-height: inherit;
    }
  }

  &--align-left {
    text-align: left;
  }

  &--align-center {
    text-align: center;
  }

  &--align-right {
    text-align: right;
  }

  &--color-primary {
    color: $cgt-text-default;
  }

  &--color-secondary {
    color: $cgt-text-lead;
  }
}
