.cgt-link {
  position: relative;
  color: $link-primary;
  line-height: normal;
  font-weight: normal;
  font-size: 16px;
  text-decoration: none;
  transition-duration: $global-animation-time;
  transition-property: color;

  &::after {
    position: absolute;
    bottom: -4px;
    left: 0;
    width: 100%;
    height: 1px;
    border-bottom: 1px solid transparent;
    transition-duration: $global-animation-time;
    transition-property: border-color;

    content: '';
  }

  &:hover {
    color: $link-primary-hover;

    &.cgt-link::after {
      border-color: $link-primary-hover;
    }
  }

  &:active {
    color: $link-primary-active;

    &.cgt-link::after {
      border-color: $link-primary-active;
    }
  }

  &:disabled,
  &[disabled] {
    color: $link-primary-disabled;
    cursor: not-allowed;

    &.cgt-link::after {
      border-color: transparent;
    }
  }

  &--dark {
    color: $cgt-heading-primary;

    &:hover {
      color: $cgt-heading-primary;

      &.cgt-link::after {
        border-color: $cgt-heading-primary;
      }
    }
  }
}
