@mixin mobile-s {
	@media (min-width: $breakpoint-s) { // 576px
		@content;
	}
}

@mixin tablet {
	@media (min-width: $inner-breakpoint-tablet) { // 768px
		@content;
	}
}

@mixin tablet-m {
	@media (min-width: $breakpoint-m) { // 964px
		@content;
	}
}

@mixin desktop-l {
	@media (min-width: $breakpoint-l) { // 1256px
		@content;
	}
}

@mixin desktop-xl {
	@media (min-width: $breakpoint-xl) { // 1576px
		@content;
	}
}

