.photoreal {
    padding-bottom: 70px;
    overflow-x: hidden;

    @include mobile-s {
        padding-bottom: 112px;
    }

    &__image-wrap {
        text-align: center;
        margin-top: 32px;
        position: relative;

        @include mobile-s {
            margin-top: 56px;
        }

        @include tablet-m {
            margin-top: 0;
            text-align: right;
        }

        img {
            box-shadow: -8px 8px 16px 0 rgba(5, 70, 75, 0.08);
            border-top-right-radius: 45px;
            border-bottom-left-radius: 45px;
            overflow: hidden;
        }
    }

    &__decoration-dots {
        position: absolute;
        z-index: -1;

        &--photoreal {

            &:nth-child(1) {
                top: -12px;
                right: -12px;

                @include mobile-s {
                    right: auto;
                    left: 0;
                    top: 50%;
                    margin-top: -98px;
                }

                @include tablet-m {
                    left: auto;
                    right: -105px;
                }
            }

            &:nth-child(2) {
                bottom: -36px;
                left: -62px;

                @include mobile-s {
                    display: none;
                }
            }
        }

        &--scalable {
            right: 0;
            bottom: -110px;
            z-index: 0;
            touch-action: none;
            pointer-events: none;

            @include mobile-s {
                right: -100px;
                left: auto;
                top: 50%;
                margin-top: -98px;
                bottom: auto;
            }

            @include tablet-m {
                right: auto;
                top: auto;
                margin: 0;
                bottom: 0;
                left: 0;
            }
        }

        &--one-model {
            top: -38px;
            right: 0;

            @include mobile-s {
                top: -52px;
            }

            @include tablet-m {
                top: 50%;
                margin-top: -98px;
                right: auto;
                left: -112px;
            }
        }
    }

    .container-fixed > .row {

        @include tablet-m {
            align-items: center;
        }
    }

    &--scalable & {

        &__image-wrap {

            @include tablet-m {
                text-align: center;
            }

            img {
                box-shadow: none;
                border-radius: 0;
            }
        }
    }

    &--scalable {
        overflow: visible;
        position: relative;
        z-index: 2;

        @include mobile-s {
            overflow: hidden;
        }

        .container-fixed {
            position: relative;
        }
    }

    &--one-model {
        padding-top: 70px;

        @include mobile-s {
            padding-top: 112px;
        }

        .photoreal__image-wrap {

            img {
                width: 100%;
            }
        }
    }
}