.thesis-description {
    padding: 0;
    display: flex;
    flex-wrap: wrap;
    align-items: flex-start;
    margin: 0 -16px;

    @include mobile-s {
        max-width: 75%;
        margin-left: -12px;
        margin-right: -12px;
    }

    @include tablet-m {
        max-width: none;
    }

    &__item {
        list-style-type: none;
        line-height: normal;
        width: 50%;
        padding: 0 16px;
        display: flex;
        align-items: center;
        margin-top: 16px;
        margin-bottom: 16px;

        @include mobile-s {
            padding: 0 12px;
            margin-top: 12px;
            margin-bottom: 12px;
        }

        .thesis-description--mobile-one-col & {
            width: 100%;

            @include mobile-s {
                width: 50%;
            }
        }
    }

    &__icon {
        flex-shrink: 0;
        margin-right: 12px;
    }

    &__text {

        span {
            font-weight: normal;
        }
    }

    &--three-col & {

        &__item {

            @include mobile-s {
                width: calc(100% / 3);
            }
        }
    }
}