// Variables
// ====================================
$cgt-checkbox-border-color:           $cgt-color-ui-500;
$cgt-checkbox-icon-color:             $cgt-color-ui-100;
$cgt-checkbox-label-color:            $cgt-color-ui-800;
$cgt-checkbox-check-theme:            $cgt-color-brand-primary-600;
$cgt-checkbox-indeterminate-theme:    $cgt-color-brand-primary-600;
$cgt-checkbox-border-color-hover:     $cgt-color-brand-primary-600;
$cgt-checkbox-disabled-theme:         $cgt-color-ui-400;

.cgt-checkbox {
  display: inline-flex;
  position: relative;
  font-size: 0;
  line-height: 0;
  align-items: flex-start;

  &:hover {

    .cgt-checkbox__input + .cgt-checkbox__text::before {
      border-color: $cgt-checkbox-border-color-hover;
    }
  }

  &__input {
    display: none;

    + .cgt-checkbox__text {
      color: $cgt-checkbox-label-color;
      line-height: 1.25;
      font-size: $global-text-size-medium;
      cursor: pointer;

      &::before {
        @include global-checkbox-uncheck();
      }
    }
  }

  i {
    color: $cgt-checkbox-icon-color;
    font-size: 12px;

    &.fa-check,
    &.fa-minus {
      position: absolute;
      top: 4px;
      left: 2px;
      opacity: 0;
      transition-property: opacity;
      transition-duration: $global-animation-time;
    }
  }

  &__input:checked {

    + .cgt-checkbox__text::before {
      border-color: $cgt-checkbox-check-theme;
      background: $cgt-checkbox-check-theme;
    }

    ~ .fa-check {
      opacity: 1;
    }
  }

  &__input:disabled + &__text {
    color: $cgt-checkbox-disabled-theme;
    cursor: default;

    &::before {
      border-color: $cgt-checkbox-disabled-theme !important;
    }
  }

  &__input:indeterminate {

    + .cgt-checkbox__text::before {
      border-color: $cgt-checkbox-indeterminate-theme;
      background-color: $cgt-checkbox-indeterminate-theme;
    }

    ~ .fa-minus {
      opacity: 1;
    }
  }
}
