$inner-step-size-number-mobile: 40px;

.step {
    background-color: $cgt-color-ui-100;
    box-shadow: -8px 8px 16px 0 rgba(5, 70, 75, 0.08);
    border-top-left-radius: 40px;
    border-bottom-right-radius: 40px;
    overflow: hidden;

    &__image-wrap {
        background-color: currentColor;
        padding: 10px;
        height: 157px;
        display: flex;
        align-items: center;
        justify-content: center;

        @include mobile-s {
            height: 182px;
            padding: 14px;
        }

        img {
            max-height: 100%;
        }

        &--back-light-blue {
            background-color: #d1f2f4;
        }
    }

    &__number {
        font-size: 12px;
        line-height: 1.67;
        font-weight: bold;
        color: currentColor;
        border-radius: 50%;
        border: 1px solid currentColor;
        width: $inner-step-size-number-mobile;
        height: $inner-step-size-number-mobile;
        display: flex;
        justify-content: center;
        align-items: center;
        background-color: $cgt-color-ui-100;
        margin: (-$inner-step-size-number-mobile / 2) auto;
        position: relative;
        z-index: 5;
    }

    &__content {
        padding: 24px 20px 20px;
        color: $cgt-text-default;

        h6 {
            font-weight: normal;
        }

        strong,
        b {
            font-weight: 600;
        }
    }

    &--full-image & {

        &__image-wrap {
            padding: 0;

            img {
                width: 100%;
                height: 100%;
                max-width: none;
                max-height: none;
                object-fit: cover;
            }
        }
    }

    &--large-height-image & {

        &__image-wrap {
            height: 300px;
        }
    }
}