.survival-guide {
    padding: 70px 0;
    background-color: $inner-global-background-section-ui;

    @include mobile-s {
        overflow: hidden;
        padding: 112px 0;
    }

    .cgt-heading-h3 {
        line-height: 1.33;
    }

    &__image-wrap {
        position: relative;
        margin-top: 18px;
        text-align: center;

        @include mobile-s {
            margin-top: 52px;
        }

        @include tablet-m {
            margin-top: 0;
        }

        & > * {
            position: relative;
            z-index: 2;
        }
    }

    &__decoration-dots {
        position: absolute;
        z-index: 1;

        &:nth-child(1) {
            left: -20px;
            bottom: -10px;

            @include mobile-s {
                left: 0;
            }

            @include tablet-m {
                right: -70px;
                left: auto;
                bottom: -40px;
            }
        }

        &:nth-child(2) {
            right: 0;
            top: 50%;
            transform: translateY(-50%);

            @include mobile-s {
                transform: none;
                top: -60px;
                right: -120px;
            }

            @include tablet-m {
                display: none;
            }
        }
    }

    .description {
        z-index: 4;
    }

    .row {

        @include tablet-m {
            align-items: center;
        }
    }
}