.arsenal-tabs {

    @include tablet-m {
        display: flex;
        align-items: center;
    }

    &__tab-link {
        text-decoration: none;
        display: inline-block;
        font-size: 16px;
        font-weight: 600;
        line-height: 1.75;
        color: $cgt-color-ui-800;
        display: flex;
        align-items: center;
        position: relative;
        z-index: 2;

        @include tablet {
            font-weight: normal;
            line-height: 1.25;
            color: $cgt-color-ui-700;
            height: 68px;
            padding: 0 12px;
            border-bottom: 2px solid transparent;
            transition-property: border-color, color;
            transition-duration: $global-animation-time;
        }

        @include tablet-m {
            width: auto;
            display: inline-flex;
            height: auto;
            border-bottom: none;
        }

        &.active {

            @include tablet {
                color: $cgt-color-ui-800;
                font-weight: 600;
                border-bottom-color: $cgt-color-brand-primary-600;
            }

            @include tablet-m {
                border-bottom: none;

                span:last-child {
                    @include tablet-m {
                        border-bottom-color: $cgt-color-brand-primary-600;
                    }
                }
            }

        }

        span {

            &:first-child {
                margin-right: 12px;
                flex-shrink: 0;
            }

            &:last-child {

                @include tablet-m {
                    border-bottom: 2px solid transparent;
                    transition-property: border-color;
                    transition-duration: $global-animation-time;
                }
            }
        }
    }

    &__item-content {
        margin-top: 28px;
        position: relative;

        @include tablet {
            display: none;
            margin-top: 0;
        }

        @include desktop-xl {
            width: 547px;
        }

        &.active {

            @include tablet {
                display: block;
            }
        }

        img {
            width: 100%;
            box-shadow: 0 10px 20px 0 rgba(5, 70, 75, 0.06);
            position: relative;
            z-index: 2;
            border-radius: 8px;
            overflow: hidden;

            @include mobile-s {
                box-shadow: 0 24px 48px 0 rgba(5, 70, 75, 0.08);
            }
        }
    }

    &__decoration-dots {
        position: absolute;
        z-index: 1;
        left: -25px;
        top: 50%;
        transform: translateY(-50%);

        @include mobile-s {
            display: none;
        }
    }

    &__tab {
        margin-bottom: 80px;

        @include tablet {
            flex-shrink: 0;
            width: 25%;
            margin-bottom: 0;
        }

        @include tablet-m {
            width: auto;
            margin-top: 22px;
            margin-bottom: 22px;
        }

        &:nth-child(3) {

            .arsenal-tabs__decoration-dots {
                left: auto;
                right: 10px;
                top: -62px;
                transform: none;
            }
        }

        &:nth-child(4) {

            .arsenal-tabs__decoration-dots {

                &:nth-child(1) {
                    transform: none;
                    top: 40px;
                }

                &:nth-child(2) {
                    transform: none;
                    top: auto;
                    bottom: -15px;
                    left: auto;
                    right: -15px;
                }
            }
        }
    }

    &__tabs {

        @include tablet {
            display: flex;
            margin-bottom: 32px;
        }

        @include tablet-m {
            flex-direction: column;
            width: 40%;
            flex-shrink: 0;
            margin-bottom: 0;
        }
    }

    &__tabs-content {
        font-size: 0;
        line-height: 0;
        display: none;

        @include tablet {
            display: block;
        }

        @include tablet-m {
            flex-shrink: 0;
            width: 60%;
        }
    }
}