.quote {
    margin: 0;
    font-weight: normal;
    font-size: 16px;
    line-height: 1.75;
    color: $cgt-color-ui-800;

    @include mobile-s {
        font-size: 20px;
        line-height: 1.4;
    }

    &::before {
        @include sprite("quotes-top");
        content: '';
        display: block;
        margin-bottom: 8px;

        @include mobile-s {
            margin-bottom: 16px;
        }
    }

    &::after {
        @include sprite("quotes-bottom");
        content: '';
        display: block;
        margin-left: auto;
        margin-top: 8px;

        @include mobile-s {
            margin-top: 16px;
        }
    }
}