// Name:            Input
// Description:     Styles for inputs
//
// Component:       `cgt-input`
//
// Sub-objects:     `cgt-input__field-wrap`
// `cgt-input__error-message`
// `cgt-input__success-message`
// `cgt-input__arrow`
//
// Modifiers:
// `cgt-input--error`
// `cgt-input--success`
// `cgt-input--disabled`
// `cgt-input--icon-left`
// `cgt-input--icon-right`
// `cgt-input--type-number`
// `cgt-input--textarea`

// Variables
// ========================================
$cgt-input-label-color:               $cgt-color-ui-600;
$cgt-input-border-color:              $cgt-color-ui-400;
$cgt-input-back-color:                $cgt-color-ui-100;
$cgt-input-color:                     $cgt-color-ui-800;
$cgt-input-placeholder-color:         $cgt-color-ui-500;
$cgt-input-theme-error:               $cgt-color-utility-danger-600;
$cgt-input-theme-success:             $cgt-color-utility-positive-600;
$cgt-input-theme-disabled:            $cgt-color-ui-400;
$cgt-input-divider-color:             $cgt-color-ui-300;
$cgt-input-arrow-color-hover:         $cgt-color-ui-600;
$cgt-input-angle-color:               $cgt-color-ui-700;

@mixin cgt-input-icon-position($side) {
  position: absolute;
  top: 50%;
  transform: translateY(-50%);

  @if $side == 'right' {
    right: 16px;
  }

  @if $side == 'left' {
    left: 16px;
  }
}

.cgt-input {
  position: relative;
  margin-bottom: 16px;

  &__field-wrap {
    position: relative;
    line-height: 0;
  }

  &__error-message,
  &__success-message {
    display: none;
    width: 100%;
    padding-top: 8px;
    font-size: $global-text-size-small;
    line-height: normal;
    font-weight: 600;

    &::first-letter {
      text-transform: uppercase;
    }
  }

  &__error-message {
    color: $cgt-input-theme-error;
  }

  &__success-message {
    color: $cgt-input-theme-success;
  }

  &__arrow {
    position: absolute;
    top: 50%;
    right: 16px;
    transform: translateY(-50%);

    i {
      display: block;
      position: static !important;
      padding-right: 0 !important;
      transform: none !important;
      transition-property: color;
      font-size: 14px;
      transition-duration: $global-animation-time;
      line-height: 8px;
      cursor: pointer;

      &::after {
        display: none !important;
      }

      &:hover {
        color: $cgt-input-arrow-color-hover;
      }
    }
  }

  &--not-spaced {
    margin-bottom: 0;
  }

  label {
    color: $cgt-input-label-color;
    font-size: $global-text-size-small;
    font-weight: 600;
    line-height: normal;
  }

  input,
  textarea {
    width: 100%;
    height: 44px;
    margin: 0;
    outline: none;
    border: 1px solid $cgt-input-border-color;
    border-radius: 1px;
    background-color: $cgt-input-back-color;
    padding-right: 16px;
    padding-left: 16px;
    color: $cgt-input-color;
    font-family: $global-font-family-primary;
    font-size: $global-text-size-medium;
    font-weight: normal;
    transition-duration: $global-animation-time;
    transition-property: border-color, box-shadow;
    appearance: none;

    &::placeholder {
      color: $cgt-input-placeholder-color;
    }

    &:focus {
      @include global-focus();
    }
  }

  textarea {
    min-height: 116px;
    padding-top: 16px;
    padding-bottom: 16px;
  }

  [type="date"] {
    padding-top: 8px;
    padding-bottom: 8px;
  }

  // Hide validation icons for the default field
  .fa-times,
  .fa-check {
    display: none;
  }

  &--error {
    margin-bottom: 12px;

    .cgt-input__error-message {
      display: block;
    }

    input,
    textarea {
      border-color: $cgt-input-theme-error;
      padding-right: 44px;
    }

    .fa-times {
      @include cgt-input-icon-position('right');
      display: inline;
      top: 23px;
      color: $cgt-input-theme-error;
    }
  }

  &--success {
    margin-bottom: 24px;

    .cgt-input__success-message {
      display: block;
    }

    input,
    textarea {
      border-color: $cgt-input-theme-success;
      padding-right: 44px;
    }

    .fa-check {
      @include cgt-input-icon-position('right');
      display: inline;
      top: 23px;
      color: $cgt-input-theme-success;
    }
  }

  &--disabled {
    label,
    i {
      color: $cgt-input-theme-disabled;
    }

    input,
    textarea {
      border-color: $cgt-input-theme-disabled;
      resize: none;
      cursor: not-allowed;

      &::placeholder {
        color: $cgt-input-theme-disabled;
      }
    }

    .fa-angle-right {
      color: $cgt-input-theme-disabled !important;
    }
  }

  &--icon-left {

    i {
      @include cgt-input-icon-position('left');
    }

    input {
      padding-left: 44px;
    }
  }

  &--icon-right {

    i {
      @include cgt-input-icon-position('right');
    }

    input {
      padding-right: 44px;
    }
  }

  &--type-number {

    i {
      padding-right: 12px;

      &::after {
        position: absolute;
        top: 50%;
        right: 0;
        z-index: 11;
        width: 1px;
        height: 28px;
        background-color: $cgt-input-divider-color;
        transform: translateY(-50%);
        content: '';
      }
    }

    input {
      width: 92px;
      -moz-appearance: textfield;

      &::-webkit-inner-spin-button,
      &::-webkit-outer-spin-button {
        margin: 0;
        -moz-appearance: none;
        -webkit-appearance: none;
        appearance: none;
      }
    }

    &.cgt-input--icon-left input {
      width: 124px;
      padding-left: 50px;
    }
  }

  &--textarea {
    display: flex;
    flex-wrap: wrap;

    .cgt-input__field-wrap {
      width: 100%;
    }

    label,
    .cgt-input__error-message,
    .cgt-input__success-message {
      width: auto;
      max-width: 50%;
      margin-bottom: 8px;
      align-self: flex-end;
    }

    &.cgt-input--error {
      .cgt-input__error-message {
        position: static;
        margin-left: auto;
        padding-left: 4px;
      }

      label {
        padding-right: 4px;
      }
    }

    &.cgt-input--success {
      .cgt-input__success-message {
        position: static;
        margin-left: auto;
        padding-left: 4px;
      }

      .fa-check {
        position: static;
        margin-left: auto;
        align-self: flex-end;
      }

      label {
        padding-right: 4px;
      }
    }

    &__icon-container {
      position: absolute;
      right: 1px;
      bottom: 1px;
      z-index: 1;
      background: $cgt-input-back-color;
      padding-right: 3px;
      padding-bottom: 1px;
      touch-action: none;
      pointer-events: none;
    }

    &__icon {
      color: $cgt-input-angle-color;
      font-size: 11px;;
    }
  }
}
