// Name:            Tags
// Description:     Styles for tags
//
// Component:       `cgt-tag`
//
// Sub-objects:     ``
//
// Modifiers:
// `cgt-tag--border`
// `cgt-tag--rounded`
//
// States:          ``

// Variables
// ====================================================

.cgt-tag {
  display: inline-flex;
  border-radius: 1px;
  padding: 4px 8px;
  font-weight: 600;
  line-height: 1.33;
  font-size: $global-text-size-small;
  align-items: center;

  &__text + i {
    margin-left: 8px;

    &.fa-times {
      font-size: 14px;
      cursor: pointer;
    }
  }

  &--border {
    border: 1px solid currentColor;
    padding: 3px 7px;
  }

  &--rounded {
    border-radius: 12px;
    padding-right: 12px;
    padding-left: 12px;

    &.cgt-tag--border {
      padding-right: 11px;
      padding-left: 11px;
    }
  }

  + .cgt-tag {
    margin-left: 4px;
  }
}
