.swiper {

    &-container {
        margin: 0 (-$inner-global-margin-container-xs);
        padding: 0 $inner-global-margin-container-xs;

        @include tablet-m {
            padding: 0;
            margin: 0;
        }
    }

    &-button-prev,
    &-button-next {
        color: $cgt-color-brand-primary-600;
        cursor: pointer;
        line-height: 0;
        padding: 4px 7px;
        touch-action: auto;
        pointer-events: all;
    }

    &-pagination {
        display: flex;
        justify-content: center;
    }

    &-pagination-bullet {
        display: inline-block;
        width: 6px;
        height: 6px;
        background-color: $inner-global-bullets;
        border-radius: 50%;
        overflow: hidden;
        margin: 0 4px;

        &-active {
            background-color: $cgt-color-brand-primary-600;
        }
    }
}