.vertical-steps {
    margin: 0;
    padding: 0;

    @include mobile-s {
        display: flex;
        flex-wrap: wrap;
        display: grid;
        grid-template-columns: repeat(2, 1fr);
        grid-gap: 28px;
    }

    &__item {
        list-style-type: none;
        margin-bottom: 20px;

        @include mobile-s {
            grid-column-start: span 1;
            margin-bottom: 0;
        }

        &:nth-child(even) {

            @include mobile-s {
                grid-row-start: span 2;
                display: flex;
                align-items: center;

                & > * {
                    width: 100%;
                }
            }

            .step {
                @include mobile-s {
                    border-radius: 0;
                    border-top-right-radius: 40px;
                }
            }
        }

        &:nth-child(odd) {
            &:not(:first-child) {
                .step {
                    @include mobile-s {
                        border-radius: 0;
                        border-bottom-left-radius: 40px;
                    }
                }
            }
        }

        &:first-child {
            .step {
                @include mobile-s {
                    border-radius: 0;
                    border-top-left-radius: 40px;
                }
            }
        }
    }
}