.spin-zoom {
    padding-bottom: 70px;
    overflow: hidden;

    @include mobile-s {
        padding-bottom: 112px;
    }

    @include tablet-m {
        overflow: visible;
    }

    &:first-child {
        padding-top: 40px;

        @include mobile-s {
            padding-top: 64px;
        }

        @include tablet-m {
            padding-top: 96px;
        }
    }

    &__text {
        position: relative;
        z-index: 2;
        margin-bottom: 32px;

        @include mobile-s {
            margin-bottom: 60px;
        }

        @include tablet-m {
            margin-bottom: 0;
        }
    }

    &__image-wrap {
        position: relative;
        z-index: 1;

        img {
            border-top-right-radius: 40px;
            border-bottom-left-radius: 40px;
            overflow: hidden;
            box-shadow: -8px 8px 16px 0 rgba(5, 70, 75, 0.08);
            width: 100%;
            position: relative;
            z-index: 2;

            @include desktop-xl {
                max-width: none;
                width: calc(100% + 95px);
            }
        }
    }

    &__decoration-dots {
        position: absolute;
        z-index: 1;

        &:nth-child(1) {
            bottom: -10px;
            left: -20px;

            @include mobile-s {
                left: auto;
                right: -5px;
                bottom: -96px;
            }

            @include tablet-m {
                right: 185%;
                bottom: -195px;
            }
        }

        &:nth-child(2) {
            display: none;

            @include mobile-s {
                display: block;
                left: -130px;
                top: -55px;
            }

            @include tablet-m {
                display: none;
            }
        }
    }

    &__play {
        position: absolute;
        top: 50%;
        left: 50%;
        transform: translate(-50%, -50%);
        z-index: 4;
    }

    &__video-wrap {
        position: relative;
        display: block;
        line-height: 0;

        img,
        &::before {
            border-radius: 4px;
            overflow: hidden;

            @include mobile-s {
                border-radius: 0;
                border-top-right-radius: 40px;
                border-bottom-left-radius: 40px;
            }
        }

        img {
            filter: blur(1px);
        }

        &::before {
            content: '';
            position: absolute;
            z-index: 3;
            background-color: rgba(249, 253, 254, 0.48);
            display: block;
            width: 100%;
            height: 100%;

            @include mobile-s {
                background-color: rgba(27, 28, 31, 0.31);
            }
        }
    }

    &--augmented-reality {
        position: relative;
        overflow: hidden;

        & > .spin-zoom__decoration-dots {

            &:nth-child(1) {
                right: 30px;
                top: -35px;

                @include mobile-s {
                    display: none;
                }

                @include tablet-m {
                    display: block;
                    left: 4%;
                    right: auto;
                    top: 50%;
                    transform: translateY(-50%);
                }
            }

            &:nth-child(2) {
                right: -60px;
                top: 50%;
                transform: translateY(-50%);

                @include mobile-s {
                    display: none;
                }
            }
        }

        .spin-zoom__image-wrap > .spin-zoom__decoration-dots {

            &:nth-child(1) {
                left: -50px;
                bottom: -8px;

                @include mobile-s {
                    left: auto;
                    bottom: auto;
                    right: -45px;
                    top: -80px;
                }

                @include tablet-m {
                    top: 0;
                    right: -154px;
                }
            }

            &:nth-child(2) {
                right: -50px;
                bottom: -50px;

                @include mobile-s {
                    display: none;
                }
            }
        }
    }

    &--augmented-reality & {

        &__decoration-dots {
            position: absolute;
            z-index: 1;
            left: auto;
            top: auto;
            right: auto;
            bottom: auto;
            display: block;
        }
    }
}