.home-appliances-description {
    padding: 70px 0;
    overflow: hidden;

    @include mobile-s {
        padding: 112px 0;
    }

    strong {
        font-weight: 600;
    }

    &__wrap {

        @include tablet-m {
            align-items: center;
        }
    }

    &__text {
        margin-bottom: 22px;

        @include mobile-s {
            margin-bottom: 28px;
        }

        @include tablet-m {
            margin-bottom: 0;
        }
    }

    &__image-wrap {
        margin: 0 -20px;
        position: relative;

        @include mobile-s {
            margin: 0;
        }

        img {
            position: relative;
            z-index: 3;

            @include mobile-s {
                box-shadow: -8px 8px 16px 0 rgba(5, 70, 75, 0.08);
                border-top-left-radius: 40px;
                border-bottom-right-radius: 40px;
            }

            @include desktop-xl {
                margin-left: -170px;
                width: 630px;
                max-width: none;
            }
        }
    }

    &__decoration-dots {
        position: absolute;
        z-index: 1;

        &:nth-child(1) {
            display: none;

            @include mobile-s {
                display: block;
                right: -116px;
                bottom: -60px;
            }

            @include tablet-m {
                right: auto;
                left: 50%;
                transform: translateX(-50%);
            }
        }
    }
}