// Variables
// =================================
$cgt-slider-rail-color:             $cgt-color-ui-400;
$cgt-slider-track-color:            $cgt-color-brand-primary-600;
$cgt-border-color-thumb:            $cgt-color-brand-primary-600;
$cgt-back-color-thumb:              $cgt-color-ui-100;

.cgt-slider {
  display: flex;
  margin-left: -12px;
  flex-wrap: wrap;
  justify-content: space-between;
  align-items: center;

  @media (min-width: $breakpoint-s) {
    flex-wrap: nowrap;
    justify-content: flex-start;
  }

  > div {
    margin-left: 12px;
  }

  &__range {
    display: inline-block;
    position: relative;
    width: 100%;
    height: 2px;
    padding: 16px 0;
    touch-action: none;
    -webkit-tap-highlight-color: transparent;
    order: 1;
    cursor: pointer;

    @media (min-width: $breakpoint-s) {
      max-width: 297px;
      order: 0;
    }
  }

  &__rail {
    display: block;
    position: absolute;
    width: 100%;
    height: 2px;
    background-color: $cgt-slider-rail-color;
  }

  &__track {
    display: block;
    position: absolute;
    height: 2px;
    background-color: $cgt-slider-track-color;
  }

  &__thumb {
    display: flex;
    position: absolute;
    width: 16px;
    height: 16px;
    margin-top: -7px;
    margin-left: -8px;
    outline: 0;
    border: 2px solid $cgt-border-color-thumb;
    border-radius: 50%;
    background-color: $cgt-back-color-thumb;
    justify-content: center;
    align-items: center;
  }

  .cgt-input input {
    width: 64px;
  }

  .cgt-input--icon-left input {
    width: 98px;
  }
}
