// Name:            Tooltip
// Description:     Styles for tooltip
//
// Component:       `cgt-tooltip`
//
// Sub-objects:
// `cgt-tooltip__head`
// `cgt-tooltip__text`
//
// Modifiers:
// `cgt-tooltip--top`
// `cgt-tooltip--left`
//
// States:          ``

// Variables
// ====================================================
$cgt-tooltip-back-color:                              $cgt-color-ui-800;
$cgt-tooltip-color:                                   $cgt-color-ui-100;
$cgt-tooltip-link-color:                              $cgt-color-brand-primary-600;
$cgt-tooltip-arrow-width:                             5px;

.cgt-tooltip-container {
  position: relative;

  .cgt-tooltip {
    position: absolute;
    z-index: $global-tooltip-z-index;
  }

  > .cgt-tooltip {
    display: none;

    &--top,
    &--bottom {
      left: 50%;
      transform: translate(-50%, 0);
    }

    &--left,
    &--right {
      top: 50%;
      transform: translate(0, -50%);
    }

    &--top {
      bottom: calc(100% + 8px);
    }

    &--bottom {
      top: calc(100% + 8px);
    }

    &--right {
      left: calc(100% + 8px);
    }

    &--left {
      right: calc(100% + 8px);
    }
  }

  &:hover {
    > .cgt-tooltip {
      display: inline-block;
    }
  }
}

.cgt-tooltip {
  display: inline-block;
  position: relative;
  max-width: 208px;
  border-radius: 1px;
  background-color: $cgt-tooltip-back-color;
  padding: 4px 8px;
  color: $cgt-tooltip-color;
  font-size: 12px;

  &::after {
    position: absolute;
    border: $cgt-tooltip-arrow-width solid transparent;
    content: '';
  }

  &--top {
    &::after {
      bottom: -($cgt-tooltip-arrow-width * 2);
      left: 50%;
      border-top: $cgt-tooltip-arrow-width solid $cgt-tooltip-back-color;
      transform: translateX(-50%);
    }
  }

  &--right {
    &::after {
      top: 50%;
      left: -($cgt-tooltip-arrow-width * 2);
      border-right: $cgt-tooltip-arrow-width solid $cgt-tooltip-back-color;
      transform: translateY(-50%);
    }
  }

  &--left {
    &::after {
      top: 50%;
      right: -($cgt-tooltip-arrow-width * 2);
      border-left: $cgt-tooltip-arrow-width solid $cgt-tooltip-back-color;
      transform: translateY(-50%);
    }
  }

  &--bottom {
    &::after {
      top: -($cgt-tooltip-arrow-width * 2);
      left: 50%;
      border-bottom: $cgt-tooltip-arrow-width solid $cgt-tooltip-back-color;
      transform: translateX(-50%);
    }
  }

  &__head {
    margin: 0;
    font-weight: 600;
    line-height: 1.33;
  }

  &__text {
    margin: 0;
    font-weight: normal;
    line-height: 1.67;

    a {
      color: $cgt-tooltip-link-color;
      text-decoration: none;
    }
  }
}
