.is-hidden {
	// stylelint-disable-next-line declaration-no-important
	display: none !important;
}

.for-desktop {
	@media (max-width: ($breakpoint-m - 1px)) {
		// stylelint-disable-next-line declaration-no-important
		display: none !important;
	}
}

.for-mobile {
	@include tablet-m {
		// stylelint-disable-next-line declaration-no-important
		display: none !important;
	}
}

.clearfix {
	overflow: auto;

	&::after {
		content: "";
		display: table;
		clear: both;
	}
}

.replace-order {
	& > *:first-child {
		@include tablet-m {
			order: 1;
		}
	}
}

.background-section-light {
	background-color: $inner-global-background-section-ui;
}

.text-link {
	color: $cgt-color-brand-primary-800;
}