// Breakpoints for mobile first
// ===================================
$breakpoint-s: 576px;
$breakpoint-m: 964px;
$breakpoint-l: 1256px;
$breakpoint-xl: 1576px;
$breakpoint-xxl: 1848px;

// Number of breakpoint (for some cycles or mixins)
// ===================================
$global-number-breakpoint: 5;

// Animation
// ===================================
$global-animation-time: 0.3s;

// Fixed container width
// ===================================
$global-container-width-m: 852px;
$global-container-width-l: 1128px;
$global-container-width-xl: 1416px;
$global-container-width-xxl: 1728px;

// Number of columns in the grid
// ===================================
$global-number-columns: 12;

// Outer margins of container
// ===================================
$global-margin-container-xs: 12px;
$global-margin-container-l: 24px;

// Typography
// ===================================
$global-font-family-primary: 'Source Sans Pro', Helvetica, Arial, sans-serif;
$global-text-size-large: 20px;
$global-text-size-medium: 16px;
$global-text-size-small: 12px;

// Z-indexes
// ===================================
$global-modal-z-index: 1000;
$global-tooltip-z-index: 100;

// Colors
// ===================================
// Global
$global-background-color: #f5f7fb;

// Brand colors
$cgt-color-brand-primary-200: #c7eaed;
$cgt-color-brand-primary-400: #3bccd8;
$cgt-color-brand-primary-600: #19bcc9;
$cgt-color-brand-primary-800: #16a7b3;

$cgt-color-brand-secondary-200: #aacde6;
$cgt-color-brand-secondary-400: #005087;
$cgt-color-brand-secondary-600: #01375c;
$cgt-color-brand-secondary-800: #012b47;

// Utility colors
$cgt-color-utility-info-200: #c1dfff;
$cgt-color-utility-info-400: #58a8ff;
$cgt-color-utility-info-600: #007aff;
$cgt-color-utility-info-800: #0064d2;

$cgt-color-utility-positive-200: #d3f5e1;
$cgt-color-utility-positive-400: #55d489;
$cgt-color-utility-positive-600: #00c853;
$cgt-color-utility-positive-800: #00a143;

$cgt-color-utility-attention-200: #ffe2bd;
$cgt-color-utility-attention-400: #ffac43;
$cgt-color-utility-attention-600: #ff8f00;
$cgt-color-utility-attention-800: #df8006;

$cgt-color-utility-danger-200: #fce0e0;
$cgt-color-utility-danger-400: #f27f7f;
$cgt-color-utility-danger-600: #eb3d3d;
$cgt-color-utility-danger-800: #ce2828;

// UI colors
$cgt-color-ui-100: #fff;
$cgt-color-ui-200: #f5f7fb;
$cgt-color-ui-300: #f0f3fa;
$cgt-color-ui-400: #e6ebf5;
$cgt-color-ui-500: #aab2c1;
$cgt-color-ui-600: #9096a3;
$cgt-color-ui-700: #636770;
$cgt-color-ui-800: #1b1c1f;

// Social colors
$cgt-color-social-facebook: #3c5a99;
$cgt-color-social-twitter: #1da1f2;
$cgt-color-social-linkedin: #007bb6;
$cgt-color-social-google-plus: #dc4e41;
$cgt-color-social-pinterest: #bd081c;

// Typography common
$link-primary: $cgt-color-brand-primary-600;
$link-primary-hover: $cgt-color-brand-primary-400;
$link-primary-active: $cgt-color-brand-primary-800;
$link-primary-disabled: $cgt-color-brand-primary-200;

// Typography for light bg colors text
$cgt-text-default: $cgt-color-ui-700;
$cgt-text-lead: $cgt-color-ui-800;
$cgt-text-caption: $cgt-color-ui-800;
$cgt-text-tiny: $cgt-color-ui-700;
$cgt-heading-primary: $cgt-color-ui-800;

// Typography for dark bg colors text
$cgt-text-default-dark-bg: $cgt-color-ui-400;
$cgt-text-lead-dark-bg: $cgt-color-ui-100;
$cgt-text-caption-dark-bg: $cgt-color-ui-100;
$cgt-text-tiny-dark-bg: $cgt-color-ui-400;
$cgt-heading-primary-dark-bg: $cgt-color-ui-100;

// Shadow color
$shadow-focus: $cgt-color-brand-primary-200;

// Border colors
$border-color-primary: $cgt-color-ui-400;
$border-color-focus: $cgt-color-brand-primary-600;

// Loaders
$global-loader-rail-color: $cgt-color-ui-400;
$global-loader-track-color: $cgt-color-brand-primary-600;

// Mixins
// ===================================
@mixin global-focus {
  border-color: $border-color-focus;
  box-shadow: 0 0 0 4px $shadow-focus;
}

@mixin clear-global-focus {
  border-color: transparent;
  box-shadow: none;
}

@mixin global-checkbox-uncheck {
  display: inline-block;
  width: 16px;
  height: 16px;
  margin-top: 2px;
  margin-right: 12px;
  border: 1px solid $cgt-color-ui-500;
  border-radius: 1px;
  vertical-align: top;
  content: '';
  transition-property: border-color, background-color;
  transition-duration: $global-animation-time;
}

@mixin global-use-icon-fontawesome($font-weight) {
  font-family: 'Font Awesome 5 Pro';
  font-weight: #{$font-weight};
  -webkit-font-smoothing: antialiased;
  display: inline-block;
  font-style: normal;
  font-variant: normal;
  text-rendering: auto;
  line-height: 1;
}
