.choose-viewer {
    padding: 70px 0;
    background-color: $inner-global-background-section-ui;
    overflow: hidden;

    @include mobile-s {
        padding: 112px 0;
    }

    @include desktop-xl {
        overflow: visible;
    }

    &--ultra-high {
        background-color: #fff;
    }

    &__text {
        position: relative;
        z-index: 2;
        margin-bottom: 32px;

        @include mobile-s {
            margin-bottom: 40px;
        }

        @include tablet-m {
            margin-bottom: 0;
        }
    }

    &__image-wrap {
        position: relative;
        z-index: 1;

        img {
            box-shadow: 0 10px 20px 0 rgba(5, 70, 75, 0.06);
            border-radius: 8px;
            overflow: hidden;
            width: 100%;
            position: relative;
            z-index: 2;
        }
    }

    &__decoration-dots {
        position: absolute;
        z-index: 1;

        &:nth-child(1) {
            left: -60px;
            top: -20px;

            @include mobile-s {
                left: auto;
                right: -120px;
                top: -55px;
            }

            @include tablet-m {
                top: auto;
                bottom: -191px;
            }
        }

        &:nth-child(2) {
            bottom: -42px;
            right: -28px;

            @include mobile-s {
                display: none;
            }
        }
    }

    .row {

        @include tablet-m {
            align-items: center;
        }
    }

    &--ultra-high & {

        &__decoration-dots {

            &:nth-child(1) {
                left: auto;
                right: -20px;
    
                @include mobile-s {
                    left: auto;
                    right: -120px;
                    top: -55px;
                }
    
                @include tablet-m {
                    bottom: auto;
                    top: 50%;
                    transform: translateY(-50%);
                    left: -120px;
                }
            }
    
            &:nth-child(2) {
                bottom: -16px;
                right: auto;
                left: -60px;
    
                @include mobile-s {
                    display: block;
                    bottom: -50px;
                    left: -135px;
                }

                @include tablet-m {
                    display: none;
                }
            }
        }
    }
}