.one-image {
    padding: 70px 0;
    background-color: $inner-global-background-section-ui;
    position: relative;

    @include mobile-s {
        padding: 112px 0;
        overflow: hidden;
    }

    &__content-wrap {
        margin: 0 -20px;
        text-align: center;
        position: relative;
        z-index: 3;
    }

    &__image-wrap {
        display: inline-block;
        position: relative;

        > img {

            @include mobile-s {
                border-top-right-radius: 40px;
                border-bottom-left-radius: 40px;
                box-shadow: -8px 8px 16px 0 rgba(5, 70, 75, 0.08);
                overflow: hidden;
            }
        }
    }

    &__decoration-dots {
        position: absolute;
        z-index: 1;

        &:nth-child(1) {
            left: 25%;
            bottom: 28px;

            @include mobile-s {
                bottom: auto;
                top: 45px;
                left: -30px;
            }

            @include tablet-m {
                left: 15%;
                top: 50%;
                transform: translateY(-50%);
            }
        }

        &:nth-child(2) {
            display: none;

            @include mobile-s {
                display: block;
                bottom: 20px;
                right: -86px;
            }

            @include tablet-m {
                right: 4%;
                bottom: 0;
            }
        }
    }

    &__qr-code {
        position: absolute;
        z-index: 3;
        width: 100%;
        bottom: 10px;
        left: 0;
        padding: 0 20px;

        .is-device-desktop & {
            display: flex;
            align-items: center;
            padding: 0 63px 28px;
        }

        > img {
            flex-shrink: 0;

            .is-device-mobile &,
            .is-device-tablet & {
                display: none;
            }
        }
    }

    &__qr-text {

        &--mobile {

            @include mobile-s {
                font-size: 16px;
                line-height: 1.75;
            }

            .is-device-mobile &,
            .is-device-tablet & {
                display: block;
            }

            .is-device-desktop & {
                display: none;
            }
        }

        &--desktop {
            margin-left: 20px;

            .is-device-mobile &,
            .is-device-tablet & {
                display: none;
            }

            .is-device-desktop & {
                display: block;
            }
        }
    }
}