.augmented-realty-overview {
    padding: 70px 0;
    background-color: $inner-global-background-section-ui;
    overflow: hidden;
    position: relative;

    @include mobile-s {
        padding: 112px 0;
    }

    &__wrap {
        position: relative;
        z-index: 3;
    }

    &__content {

        @include tablet-m {
            flex-shrink: 0;
            margin-right: -102px;
            position: relative;
            z-index: 3;
            padding-bottom: 20px;
        }
    }

    &__content-item {
        margin-bottom: 20px;

        @include mobile-s {
            margin-bottom: 30px;
        }

        &:nth-child(even) {
            @include mobile-s {
                width: 540px;
                margin-left: auto;
            }

            @include tablet-m {
                margin-left: 315px;
            }
        }

        &:nth-child(odd) {

            @include mobile-s {
                width: 445px;
            }
        }
    }

    &__media {

        @include mobile-s {
            width: 445px;
            margin-left: 84px;
        }

        @include tablet-m {
            width: 755px;
            flex-shrink: 0;
            margin: 0;
        }
    }

    &__wrap {

        @include tablet-m {
            display: flex;
            align-items: flex-end;
        }
    }

    &__decoration-dots {
        position: absolute;
        z-index: 1;

        &:nth-child(1) {
            top: 30%;
            transform: translateY(-50%);
            right: 0;

            @include mobile-s {
                right: -106px;
            }

            @include tablet-m {
                right: auto;
                left: 33%;
                transform: none;
                top: 55px;
            }
        }

        &:nth-child(2) {
            display: none;

            @include mobile-s {
                display: block;
                top: 61%;
                transform: translateY(-50%);
                left: 10px;
            }
        }
    }
}