// Name:            Accordion
// Description:     Styles for accordion
//
// Component:       `cgt-accordion`
//
// Sub-objects:
// `cgt-accordion__item`
// `cgt-accordion__label`
// `cgt-accordion__head`
// `cgt-accordion__content`
//
// Modifiers:       `cgt-accordion--border`
//
// States:          `is-open`

// Variables
// ====================================================
$cgt-accordion-label-color:                           $cgt-color-ui-600;
$cgt-accordion-label-color-is-open:                   $cgt-color-ui-800;

.cgt-accordion {
  max-width: 436px;
  padding-left: 0;

  &__item {
    list-style-type: none;

    &:not(:last-child) {
      border-bottom: 1px solid $border-color-primary;
    }

    &.is-open {

      .cgt-accordion__content {
        display: block;
      }

      .cgt-accordion__label {
        color: $cgt-accordion-label-color-is-open;
        font-weight: 600;
      }

      .cgt-accordion__head i {
        transform: rotate(-180deg);
      }
    }
  }

  &__head {
    display: flex;
    padding: 12px 16px;
    cursor: pointer;
    align-items: flex-start;

    i {
      padding-top: 2px;
      transition-property: transform;
      transition-duration: $global-animation-time;
      flex-shrink: 0;
    }
  }

  &__label {
    color: $cgt-accordion-label-color;
    font-weight: normal;
    line-height: normal;
    font-size: $global-text-size-medium;
    flex-grow: 1;
  }

  &__content {
    display: none;
    margin-bottom: 12px;
    padding: 0 16px;

    > * {
      margin: 0;
    }

    a {
      color: inherit;
    }
  }

  &--border {
    border: 1px solid $border-color-primary;
  }
}
