// Name:            Button
// Description:     Styles for buttons
//
// Component:       `cgt-button`
//
// Sub-objects:     `cgt-button-group`
// `cgt-dropdown`
//
// Modifiers:
// `cgt-button--primary`
// `cgt-button--secondary`
// `cgt-button--ghost`
// `cgt-button--icon`
// `cgt-button--icon-only`
// `cgt-button--shadow`
// `cgt-button--small`
// `cgt-button--large`
// `cgt-button--width-auto`
// `cgt-button--width-expand`
//
// States:          `is-open` for dropdown

// Variables
// ====================================================
//
// Common
$cgt-button-border-radius:                  1px;
$cgt-button-shadow-color:                 rgba(0, 0, 0, 0.16);
$cgt-button-color-shadow-focus:             $cgt-color-brand-primary-200;
$cgt-button-border-color-focus:             $cgt-color-brand-primary-600;
//
// Font size
$cgt-button-font-size-default:              16px;
$cgt-button-font-size-small:                16px;
$cgt-button-font-size-large:                18px;
//
// Primary style
$cgt-button-color-primary:                  $cgt-color-ui-100;
$cgt-button-back-color-primary:             $cgt-color-brand-primary-600;
$cgt-button-back-color-primary-hover:       $cgt-color-brand-primary-400;
$cgt-button-back-color-primary-active:      $cgt-color-brand-primary-800;
$cgt-button-back-color-primary-disabled:    $cgt-color-brand-primary-200;
//
// Secondary style
$cgt-button-back-color-secondary:           $cgt-color-ui-100;
$cgt-button-border-color-secondary:         $cgt-color-ui-400;
$cgt-button-border-width-secondary:         1px;
$cgt-button-back-color-secondary-hover:     $cgt-color-ui-400;
$cgt-button-back-color-secondary-active:    $cgt-color-ui-500;
$cgt-button-border-color-secondary-active:  $cgt-color-ui-500;
$cgt-button-color-secondary-disabled:       $cgt-color-ui-400;
$cgt-button-back-color-secondary-disabled:  transparent;
$cgt-button-border-color-secondary-disabled:  $cgt-color-ui-400;
$cgt-button-icon-color-secondary:           $cgt-color-ui-700;
//
// Ghost style
$cgt-button-back-color-ghost:               transparent;
$cgt-button-color-ghost:                    $cgt-color-ui-800;
$cgt-button-icon-color-ghost:               $cgt-color-ui-700;
$cgt-button-back-color-ghost-hover:         $cgt-color-ui-400;
$cgt-button-back-color-ghost-active:        $cgt-color-ui-500;
$cgt-button-color-ghost-disabled:           $cgt-color-ui-400;
$cgt-button-back-color-ghost-disabled:      transparent;
$cgt-button-back-color-ghost-focus:         $cgt-color-ui-100;
//
// Dropdown
$cgt-dropdown-content-shadow-color:         $cgt-color-ui-400;
$cgt-dropdown-content-border-color:         $cgt-color-ui-400;
$cgt-dropdown-content-back-color:           $cgt-color-ui-100;
$cgt-dropdown-content-width:                197px;
$cgt-dropdown-color:                        $cgt-color-ui-800;
$cgt-dropdown-color-hover:                  $cgt-color-ui-300;
$cgt-dropdown-icon-color:                   $cgt-color-ui-700;

.cgt-button {
  display: inline-block;
  margin: 0;
  border: 0;
  border-radius: $cgt-button-border-radius;
  padding: 12px 24px;
  font-size: $cgt-button-font-size-default;
  font-weight: 600;
  line-height: normal;
  text-align: center;
  text-decoration: none;
  text-transform: none;
  transition-duration: $global-animation-time;
  transition-property: background-color, box-shadow, border-color, opacity;
  cursor: pointer;
  vertical-align: top;
  font-family: inherit;

  &:focus {
    outline: none;
  }

  &:disabled,
  &[disabled] {
    cursor: not-allowed;
  }

  span {
    vertical-align: middle;

    + i {
      padding-left: 8px;
    }
  }

  i {
    transform-origin: calc(50% + 4px) 50%;
    transition-duration: $global-animation-time;
    transition-property: transform;
    vertical-align: middle;

    + span {
      padding-left: 8px;
    }
  }

  &--icon {
    padding-right: 16px;
    padding-left: 16px;
  }

  &--only-icon {
    padding-right: 12px;
    padding-left: 12px;
  }

  &--primary {
    background-color: $cgt-button-back-color-primary;
    color: $cgt-button-color-primary;

    &:hover {
      background-color: $cgt-button-back-color-primary-hover;
    }

    &:active {
      background-color: $cgt-button-back-color-primary-active;
    }

    &:focus {
      @include global-focus();
    }

    &:disabled,
    &[disabled] {
      background-color: $cgt-button-back-color-primary-disabled;
    }
  }

  &--secondary {
    border: $cgt-button-border-width-secondary solid $cgt-button-border-color-secondary;
    background-color: $cgt-button-back-color-secondary;

    &:hover {
      background-color: $cgt-button-back-color-secondary-hover;
    }

    &:active {
      border-color: $cgt-button-border-color-secondary-active;
      background-color: $cgt-button-back-color-secondary-active;
    }

    &:disabled,
    &[disabled] {
      border-color: $cgt-button-border-color-secondary-disabled;
      background-color: $cgt-button-back-color-secondary-disabled;
      color: $cgt-button-color-secondary-disabled;
    }

    &:focus {
      @include global-focus();
    }

    i {
      color: $cgt-button-icon-color-secondary;
    }
  }

  &--ghost {
    border: 1px solid transparent;
    background-color: $cgt-button-back-color-ghost;
    color: $cgt-button-color-ghost;

    &:hover {
      background-color: $cgt-button-back-color-ghost-hover;
    }

    &:active {
      background-color: $cgt-button-back-color-ghost-active;
    }

    &:disabled,
    &[disabled] {
      background-color: $cgt-button-back-color-ghost-disabled;
      color: $cgt-button-color-ghost-disabled;
    }

    &:focus {
      @include global-focus();
      background-color: $cgt-button-back-color-ghost-focus;
    }

    i {
      color: $cgt-button-icon-color-ghost;
    }
  }

  &--dark-bg {
    color: $cgt-color-ui-100;
    opacity: 0.62;

    i {
      color: $cgt-color-ui-100;
    }

    &:hover,
    &:active,
    &:focus {
      @include clear-global-focus();
      opacity: 1;
      background-color: transparent;
    }
  }

  &--shadow {
    box-shadow: 0 6px 12px 1px $cgt-button-shadow-color;
  }

  &--small {
    padding: 8px 16px;
    font-size: $cgt-button-font-size-small;

    &.cgt-button--icon {
      padding-right: 12px;
      padding-left: 12px;
    }

    &.cgt-button--only-icon {
      padding-right: 8px;
      padding-left: 8px;
    }

    + .cgt-dropdown__content {
      font-size: $cgt-button-font-size-small;
    }
  }

  &--large {
    padding: 14px 32px;
    font-size: $cgt-button-font-size-large;

    &.cgt-button--icon {
      padding-right: 20px;
      padding-left: 20px;
    }

    &.cgt-button--only-icon {
      padding-right: 14px;
      padding-left: 14px;
    }
  }

  &--width-auto {
    width: auto;

    &\@s {
      @media (min-width: $breakpoint-s) {
        width: auto;
      }
    }

    &\@m {
      @media (min-width: $breakpoint-m) {
        width: auto;
      }
    }

    &\@l {
      @media (min-width: $breakpoint-l) {
        width: auto;
      }
    }

    &\@xl {
      @media (min-width: $breakpoint-xl) {
        width: auto;
      }
    }

    &\@xxl {
      @media (min-width: $breakpoint-xxl) {
        width: auto;
      }
    }
  }

  &--width-expand {
    width: 100%;

    &\@s {
      @media (min-width: $breakpoint-s) {
        width: 100%;
      }
    }

    &\@m {
      @media (min-width: $breakpoint-m) {
        width: 100%;
      }
    }

    &\@l {
      @media (min-width: $breakpoint-l) {
        width: 100%;
      }
    }

    &\@xl {
      @media (min-width: $breakpoint-xl) {
        width: 100%;
      }
    }

    &\@xxl {
      @media (min-width: $breakpoint-xxl) {
        width: 100%;
      }
    }
  }

  &-group {
    display: inline-flex;
  }

  &-group & {
    margin: 0 1px;
    border-radius: 0;
    flex-shrink: 0;

    &:first-child {
      margin-left: 0;
      border-bottom-left-radius: $cgt-button-border-radius;
      border-top-left-radius: $cgt-button-border-radius;
    }

    &:last-child {
      margin-right: 0;
      border-bottom-right-radius: $cgt-button-border-radius;
      border-top-right-radius: $cgt-button-border-radius;
    }

    &--secondary {
      margin: 0;

      &:not(:first-child) {
        margin-left: -$cgt-button-border-width-secondary;
      }
    }
  }

  &--social {
    color: $cgt-color-ui-100;

    &:hover,
    &:active {
      opacity: 0.8;
    }

    &:focus {
      @include global-focus();
    }

    &:disabled,
    &[disabled] {
      opacity: 0.6;
    }
  }

  &--facebook {
    background-color: $cgt-color-social-facebook;
  }

  &--twitter {
    background-color: $cgt-color-social-twitter;
  }

  &--linkedin {
    background-color: $cgt-color-social-linkedin;
  }

  &--google-plus {
    background-color: $cgt-color-social-google-plus;
  }

  &--pinterest {
    background-color: $cgt-color-social-pinterest;
  }
}

.cgt-dropdown {
  position: relative;

  &__content {
    position: absolute;
    top: calc(100% + 4px);
    left: 0;
    visibility: hidden;
    opacity: 0;
    z-index: 10;
    width: $cgt-dropdown-content-width;
    margin: 0;
    border: 1px solid $cgt-dropdown-content-border-color;
    background-color: $cgt-dropdown-content-back-color;
    padding: 0;
    transition-property: opacity, visibility;
    transition-duration: $global-animation-time;
    box-shadow: 0 2px 6px 0 $cgt-dropdown-content-shadow-color;


    li {
      padding: 12px 16px;
      color: $cgt-dropdown-color;
      font-size: $cgt-button-font-size-default;
      font-weight: normal;
      list-style-type: none;
      transition-duration: $global-animation-time;
      transition-property: background-color;
      cursor: pointer;

      &:hover {
        background-color: $cgt-dropdown-color-hover;
      }

      i {
        padding-right: 12px;
        color: $cgt-dropdown-icon-color;
        vertical-align: middle;
      }

      span {
        vertical-align: middle;
      }
    }
  }

  &.is-open & {

    &__content {
      visibility: visible;
      opacity: 1;
    }
  }

  &.is-open .cgt-button i {
    transform: rotate(-180deg);
  }
}
